import { POST, GET } from './index';

/**
 * 创建知识库
 * @param baseInfo 知识库简介
 * @param baseName 知识库名称
 * @param baseKind 知识库种类
 * @param courseId? 如果课程创建
 * @param createFrom 1个人 2团队 3教师 创建来源
 * @param groupId 团队创建
 * @param isPublic 是否公开 0私有 1发布
 * @param userToken
 * */ 
export const createKnowledge = (params) => 
  POST('/api/student/knowBase/createKnowBase.do', params);

/**
 * @param page 页码
 * @param userToken 用户token
 * */ 
export const queryKnowledgeList = (params) => 
  POST('/api/student/knowBase/showAllKnowBase.do', params);

/**
 * @param baseId 知识库业务主键id
 * */ 
export const getKnowledgeDetail = (params) => 
  POST(`/api/student/knowBase/selectOneBaseMsg.do?baseId=${params.baseId}`, params);

/**
 * @param knowledgeDetail 知识库详情～
*/

export const updateKnowledgeDetail = (params) => 
  POST('/api/student/knowBase/updateKnowBase.do', params)
