import React, { Component } from 'react';
import SharedTable from '../../../../public/SharedTable';
import SearchBar from '../../../../public/SearchBar';
import { Dropdown, Menu, Button } from 'antd'; 
import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';
import Icon from '../../../../public/Icon/Icon';

export default class GroupMember extends Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: '昵称',
        key: 'memberName',
        width: 240,
      },
      {
        title: '学号',
        key: 'id',
        width: 320,
        render: item => <p className="paragraph">{item.id}</p>
      },
      {
        title: '角色',
        key: 'memberRole',
        width: 320,
        render: (item, index) => {
          if (index === 0) {
            return <div className="desc-paragraph">自己</div>
          }
          return <Dropdown 
            overlay={this.renderMenu(index)}
          >
            <span className="role">
              <p>{item.memberRole}</p>
              <CaretDownOutlined style={{color: '#999'}} />
            </span>
          </Dropdown>
        }
      },
      {
        title: '操作',
        key: 'order',
        width: 80,
        render: (item, index) => {
          if (item.order === 0) {
            return <a onClick={() => this.quit(item)}>退出</a>
          } 
          return <a onClick={() => this.delete(item)}>删除</a>
        }
      }
    ]

    this.state = {
      columns,
      dataSource: [],
      checkedIds: [],
      isMenuShow: false,
    }
  }

  
  renderMenu = index => {
    return <Menu 
      onClick={e => this.selectRoot(e, index)}
    >
      <Menu.Item
        key="管理员"
        >
        <h3>管理员</h3>
        <p>拥有所有权限</p>
      </Menu.Item>
      <Menu.Item
        key="成员"
        >
        <h3>成员</h3>
        <p>拥有知识库管理权限</p>
      </Menu.Item>
      <Menu.Item
        key="只读成员"
        >
        <h3>只读成员</h3>
        <p>仅有阅读权限</p>
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item
        key="setting"
      >
        <a style={{color: '#3A92F9'}} className="settingBtn" href="">设置权限角色</a>
      </Menu.Item>
    </Menu>
  }
  
  selectRoot = (values, index) => {
    const { key } = values;
    const { dataSource } = this.state;
    dataSource[index].memberRole = key;
    this.setState({
      dataSource,
    })
  }

  quit = item => {
    console.log(item, '退出团队');
  }

  delete = item => {
    console.log(item, '删除成员')
  }

  componentDidMount() {
    const { dataSource } = this.state;
    const roles = ['管理员', '阅读者', '自己']

    for (let i = 0; i < 6; i++) {
      const order = parseInt(Math.random() * 3);
      dataSource.push({
        memberName: '丁义秋',
        id: `31161704010${i}`,
        memberRole: roles[order],
        order: i === 0 ? 0 : 1,
      })
    }

    this.setState({
      dataSource,
    })
  }

  setCheckedList(checkedIds) {
    this.setState({
      checkedIds,
    });
  }

  setMenuVisible = visible => {
    this.setState({
      isMenuShow: visible,
    })
  }

  selectRoot = e => {
    console.log(e);
    this.setMenuVisible(false);
  }

  renderEditMenu = () => {
    return <Menu 
        onClick={e => this.selectRoot(e)}
      >
      <Menu.Item
        key="管理员"
        >
        <h3>管理员</h3>
        <p>拥有所有权限</p>
      </Menu.Item>
      <Menu.Item
        key="成员"
        >
        <h3>成员</h3>
        <p>拥有知识库管理权限</p>
      </Menu.Item>
      <Menu.Item
        key="只读成员"
        >
        <h3>只读成员</h3>
        <p>仅有阅读权限</p>
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item
        key="setting"
      >
        <a style={{color: '#3A92F9'}} className="settingBtn" href="">设置权限角色</a>
      </Menu.Item>
    </Menu>
  }

  renderExtraBtn = () => {
    const { isMenuShow } = this.state;
    return <div className="batch-module flex">
      <Dropdown
        overlay={() => this.renderEditMenu()}
        visible={isMenuShow}
        onVisibleChange={(visible) => this.setMenuVisible(visible)}
      >
        <div>
          <span className="curser-pointer">批量调整权限</span>
          <Icon className="right-icon" type={isMenuShow ? 'iconlujing246' : 'iconlujing133'} />
        </div>
      </Dropdown>
      <Button type="link">批量删除</Button>
    </div>
  }

  render() {
    const { columns, dataSource } = this.state;
    return <div className="knowledge-list">
      <SearchBar searchTitle="成员管理" placeholderText="搜索成员" searchBtnText="添加成员" />
      <SharedTable 
        hasCheckedBox 
        dataSource={dataSource} 
        hasCheckedBox={true} 
        columns={columns} 
        getCheckedList={(values) => this.setCheckedList(values)}
        renderExtraBtn={() => this.renderExtraBtn()}
      /> 
    </div>
  }
}