import React, { Component } from 'react';
import PopPanel from '../PopPanel';
import { actions } from 'mirrorx';
import { Form, Select, Spin } from 'antd';
import Icon from '../Icon/Icon';
import { searchGroups } from '../../api';

const { Option } = Select;

class SelectGroupModal extends Component {
  state = {
    groupList: [],
    targetStudent: {},
    fetching: false,
  }
  
  componentDidMount = async () => {
    const ret = await searchGroups({page: 1, type: 2,});
    const groupList = ret.data.dataList;
    const userInfos = await actions.userInfo.getUserInfos();
    this.setState({
      groupList: groupList.filter(item => item.studentCode !== userInfos.studentCode).filter(clsItem => clsItem.classId === userInfos.classId),
    });
  }

  onSelectItem = (values) => {
    const { groupList } = this.state;
    const targetStudent = groupList.find(item => item.studentId === values);
    this.setState({
      targetStudent,
    });
  }

  renderWarning = () => {
    return (
      <div className="warn-content flex">
        <Icon className="icon" type="iconzujian63" />
        <div>
          很抱歉，当前模式下只能选择同班级的同学，如果有跨班组队的需要请及时联系<strong>高黎明（QQ号：3476179536）</strong>  
        </div>
      </div>
    )
  }

  render() {
    const { groupList, targetStudent, fetching } = this.state;
    const { onSelect } = this.props;
    return (
      <PopPanel
        title="加入团队"
        onOkCallBack={() => onSelect('targetStudent')}
      >
        {
          this.renderWarning()
        }
        <Form 
          className="addForm"
        >
          <Form.Item label="选择团队">
            <Select 
              showSearch
              placeholder="请选择心仪的团队"
              onChange={values => this.onSelectItem(values)}
              notFoundContent={fetching ? <Spin size="small" /> : null}
            >
              {
                groupList.map(item => (
                  <Option 
                    key={item.studentId} 
                    value={item.studentId}
                  >
                    {`${item.name} (${item.studentCode})`}
                  </Option>
                ))
              }
            </Select>
          </Form.Item>
        </Form>
      </PopPanel>
    )
  }
}

export default PopPanel.wrapper(SelectGroupModal);