import mirror, { actions } from 'mirrorx';
import { getUserInfo, getAllUsers } from '../api/user';

export default mirror.model({
  name: 'userInfo',
  initialState: {
    userCode: '',
    password: '',
    detailInfos: {},
    isNewUser: false,
    students: [],
  },
  reducers: {
    save(state, payload) {
      return {
        ...state,
        ...payload,
      }
    }
  },
  effects: {
    async getUserInfos(options, getState) {
      const { detailInfos } = getState().userInfo;
      if (!detailInfos.id || (options && options.forceUpdate)) {
        const res = await getUserInfo();
        if (!res.data) return ;
        actions.userInfo.save({
          detailInfos: res.data,
          isNewUser: res.data && !res.data.groupId,
        });
        return res.data;
      }
      return detailInfos;
    },
    async getUsers(options, getState) {
      let { students } = getState().userInfo;
      if (students.length) {
        return students;
      };

      const res = await getAllUsers();
      students = res.data;
      actions.userInfo.save({
        students,
      });
      
      return students;

    }
  }
})