import React, { Component } from 'react';
import SearchBar from '../../../public/SearchBar';
import SharedTable from '../../../public/SharedTable';
import Icon from '../../../public/Icon/Icon';
import { queryKnowledgeList, queryKnowledgeListByKeywords } from '../../../api';
import moment from 'moment';

import './index.css';

const kindIcons = {
  '1': 'iconzu112-copy',
  '2': 'iconzu400',
}

function formData(data) {
  return data.map(knowledgeItem => ({
    ...knowledgeItem,
    id: knowledgeItem.baseId,
    behavior: knowledgeItem.parentName,
    icon: () => <Icon type={kindIcons[knowledgeItem.baseKind]} />,
    updatedTime: new moment(knowledgeItem.createTime).format('YYYY-MM-DD HH:mm:ss'),
    belong: knowledgeItem.parentName,
  }));
}

export default class Knowledges extends Component {
  constructor(props) {
    super(props);

    let { columns } = this.props;

    if (!columns) {
      columns = [
        {
          title: '名称',
          key: 'baseName',
          width: 280,
        },
        {
          title: '归属',
          key: 'belong',
          width: 240,
          className: 'desc-paragraph',
        },
        {
          title: '简介',
          key: 'baseInfo',
          width: 360,
          className: 'desc-paragraph',
        }
      ];
    }

    this.state = {
      columns,
      knowledgeList: [],
      loading: true,
    };
  }

  async componentDidMount() {
    const ret = await this.fetchData(1);
    this.setState({
      knowledgeList: formData(ret.data.dataList),
      loading: false,
    })
  }

  async fetchData(page = 1) {
    this.setState({
      loading: true,
    });
    return await queryKnowledgeList({page});
  }

  onSearch = async (value) => {
    this.setState({
      loading: true,
    })
    const ret = await queryKnowledgeListByKeywords({
      keyWord: value,
    });

    this.setState({
      knowledgeList: formData(ret.data),
      loading: false,
    });

  }
  
  render() {
    const { columns, knowledgeList, loading } = this.state;
    return <div className="knowledge-list">
    <SearchBar 
      onSearch={this.onSearch} 
      placeholderText="搜索知识库" 
      searchTitle="知识库" 
      searchBtnText="新建知识库" 
      onClick={() => {
        this.props.history.push('/knowledge/createNew')
      }}
    />
    <SharedTable
      loading={loading}
      parentPath="#/knowledge/detail/" 
      columns={columns} 
      dataSource={knowledgeList} 
    />
  </div>
  }
}