import React, { Component } from 'react';
import { Button, message } from 'antd';
import KnowledgeItem from './KnowledgeItem/KnowledgeItem';
import StudentSelectModal from '../../../../public/StudentSelectModal';
import { addNumberToGroup, getGroupDetail } from '../../../../api';
import Icon from '../../../../public/Icon/Icon';

import './index.css';

export default class GroupMainContent extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      knowledgeList: [],
      isShowGuide: true,
    }
  }

  async componentDidMount() {
    const { groupId, knowledgeList } = this.state;
    for (let i = 0; i < 5; i++) {
      knowledgeList.push({
        id: `2ajhsduijah${i}`,
        knowledgeName: '项目方案',
        desc: '跟进项目进度，存储项目文档',
        updatedDocs: [
          {
            id: `3jhjks${i}dsd`,
            title: '产品研发进度表',
            updatedTime: '今天 14:24',
          },
          {
            id: `3jhjks${i}dsd33`,
            title: '产品研发计划',
            updatedTime: '今天 14:24',
          },
          {
            id: `wjhajhd${i}`,
            title: '项目运营方案',
            updatedTime: '今天 14:24',
          }
        ]
      })
    }

    this.setState({
      knowledgeList,
    })
  }

  addNumber = () => {
    StudentSelectModal.show({
      onSelect: this.addItemCallback,
    })
  }

  addItemCallback = async (targetStudent) => {
    const { studentId, studentCode } = targetStudent;
    const { memberList } = this.props;
    if (memberList.filter(item => item.studentCode === studentCode).length) {
      message.warn('该成员已经在团队中，请勿重新添加');
      return;
    };

    const { groupId } = this.props;
    const ret = await addNumberToGroup({
      groupId,
      studentCode,
      studentId,
    });
    if(ret.code === 200) {
      message.success('添加成功');
    };

    this.props.fetchPage();
  }

  addNewKnowledge = () => {
    this.props.history.push(`/knowledge/createNew/${this.props.groupId}`);
  }

  render() {
    const { knowledgeList, isShowGuide } = this.state;
    return (
      <div className="main-section">
        {isShowGuide && <div className="guide-card">
          <Icon onClick={() => {this.setState({isShowGuide: false})}} className="closeBtn" type="iconzujian51" />
          <img style={{height: '175px', marginRight: '21px'}} src={require('../../../../assets/images/group_guide01.png')} alt=""/>
          <div className="guide-text">
            <h3>欢迎来到鲸落团队</h3>
            <p className="desc-paragraph">
              点击下方添加成员或新建知识库，与团队成员一起创作与分享知识。
            </p>
            <div className="actionBtns">
              <Button onClick={() => this.addNumber()} type="primary">添加成员</Button>
              <Button onClick={this.addNewKnowledge} className="defaultBtn">新建知识库</Button>
            </div>
          </div>
        </div>}
        <div className="knowledge-list-cards">
          {
            knowledgeList.map(item => 
              <KnowledgeItem key={item.id} {...item} />
            )
          }
        </div>
      </div>
    )
  }
}