import React, { Component } from 'react';
import { Input, Dropdown, Menu } from 'antd';
import Icon from '../../../public/Icon/Icon';
import HasNoData from '../../../public/HasNoData';

import './index.css';

const selectedOptions = {
  all: '全部',
  docs: '文档',
  dashboard: '画板',
  knowledge: '知识专栏',
  learnNote: '学习笔记'
}

export default class Knowledge extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedKeys: ['all'],
      isVisible: false,
    }
  }

  onTypeSelect = types => {
    this.setState({
      selectedKeys: [types.key],
      isVisible: false,
    })
  }

  renderMenu = () => {
    const { selectedKeys } = this.state;
    return (
      <Menu selectedKeys={selectedKeys} onClick={this.onTypeSelect}>
        {
          Object.keys(selectedOptions).map(key => {
            return (
              <Menu.Item key={key}>
                {selectedOptions[key]}
              </Menu.Item>
            )
          }) 
        }
      </Menu>
    )
  }

  onVisibleChange = isVisible => {
    this.setState({
      isVisible,
    });
  }

  render() {
    const { selectedKeys, isVisible } = this.state;
    const { knowledgeList } = this.props;
    return (
      <div className="person-knowledge">
        <div className="header flex">
          <Input.Search 
            className="search" 
            placeholder="搜索知识库"
          />
          <Dropdown 
            overlay={this.renderMenu()}
            visible={isVisible}
            onVisibleChange={(isVisible) => this.onVisibleChange(isVisible)}
          >
            <div className="curser-pointer">
              类型：{selectedOptions[selectedKeys[0]]} &nbsp;
              <Icon type={!isVisible ? 'iconduobianxing3' : 'iconduobianxing22'} />
            </div>
          </Dropdown>
        </div>
        <div className="main-content">
          {
            knowledgeList.length === 0 ? 
            <HasNoData /> : (
              <ul className="knowledge-list">
                {
                  knowledgeList.map((item, index) => {
                    return <li key={index} className="knowledge-item flex">
                      <Icon className="icon" type="iconzu112-copy" />
                      <dl>
                        <dt>{item.name}</dt>
                        <dd className="desc-paragraph">{item.desc}</dd>
                      </dl>
                      <span className="update-time">
                        {item.updateTime}
                      </span>
                    </li>
                  })
                }
              </ul>
            )
          }
        </div>
      </div>
    )
  }
}