import React, { Component } from 'react';
import { Button, Tooltip } from 'antd';
import HasNoData from '../../public/HasNoData';
import Icon from '../../public/Icon/Icon';
import { NavLink, Router, Switch, Route } from 'react-router-dom';
import { createHashHistory } from 'history';
import Knowledge from './Knowledges/Knowledge';
import Concern from './Concern/Concern';
import Noticer from './Noticer/Noticer';
import moment from 'moment';
import { connect, actions } from 'mirrorx';

import './index.css';

const history = createHashHistory();
class PersonalCenter extends Component {
  constructor(props) {
    super(props);

    this.state = {
      userNick: '黑白之间',
      liked: 10,
      concerns: 25,
      userName: '丁义秋',
      userCode: '3116107040124',
      majorCode: '工业设计',
      groupList: [],
      knowledgeList: [],
    }


    if (this.props.location.pathname == "/personal_center") {
      this.props.history.replace('/personal_center/knowledge');
    }
  }

  async componentDidMount() {
    const { groupList, knowledgeList } = this.state;
    const userInfos = await actions.userInfo.getUserInfos();
    const date = moment().format("LT");

    for (let i = 0; i < 9; i++) {
      groupList.push({
        name: '交互设计专业设计流程团队',
        // groupAvatar: '默认头像',
      });

      knowledgeList.push({
        name: '设计职业心得',
        desc: '知识库简介：可见范围为“互联网可见”知识库默认开启，取消勾选后所有用户都无法评论。',
        updateTime: date,
        id: `asdhjakhdkj${i}`
      })
    };

    this.setState({
      groupList,
      knowledgeList,
      userNick: userInfos.nickName || userInfos.name,
      userName: userInfos.name,
      userCode: userInfos.studentCode,
      majorCode: `工设${userInfos.classId}`,
    });
  }

  render() {
    const { 
      userNick, 
      liked, 
      concerns, 
      userName, 
      userCode, 
      majorCode, 
      groupList, 
      knowledgeList 
    } = this.state;

    return (
      <div className="personal-center">
        <div className="left-area border">
          <div className="userInfo">
            <div className="avatar-img">

            </div>
            <h2>{userNick}</h2>
            <Button 
              onClick={() => {this.props.history.push('/personalInfo_edit')}}
              style={{width: 112, marginBottom: 21}} 
              className="defaultBtn"
            >
                编辑资料
            </Button>
            <div className="concern-infos flex">
              <span>关注了</span>
              <span>关注者</span>
            </div>
            <div className="concern flex">
              <span>
                {liked}
              </span>
              <span>
                {concerns}
              </span>
            </div>
          </div>
          <div className="basic-info flex">
            <div className="top-section flex">
              <p>姓名：{userName}</p>
              <p>学号：{userCode}</p>
            </div>
            <div className="bottom-bar">
              <p>专业班级：{majorCode}</p>
            </div>
          </div>
          <div className="group-info">
            <h2>团队</h2>
            {
              groupList.length > 0 ? (
                <div className="flex group-list-person">
                  {groupList.map((item, index) => {
                    if (index >= 3) {
                      return null;
                    } else {
                      return (
                        <a href="#/group/detail/a23jh280w2" className="group-item">
                          <dt className="group-avatar">
                            {
                              item.groupAvatar ? 
                                item.groupAvatar : 
                                <img src={require('../../assets/images/default_avatar.png')} />
                            }
                          </dt>
                          <Tooltip title={item.name}>
                            <div className="flex">
                              <dd className="group-name">
                                {item.name}
                              </dd>
                              <Icon type="iconlujing243" />
                            </div>   
                          </Tooltip>
                        </a>
                      )
                    }
                  })}
                </div>
              ) : <HasNoData />
            }
          </div>
        </div>
        <div className="right-area border">
        <div className="tab-header flex">
          <div className="nav flex">
            <NavLink 
              key="knowledge" 
              className="group-menu-item" 
              activeClassName="active" 
              to={`/personal_center/knowledge`}
            >
              知识库
            </NavLink>
            <NavLink 
              key="concern" 
              className="group-menu-item" 
              activeClassName="active" 
              to={`/personal_center/concern`}
            >
              关注了
            </NavLink>
            <NavLink 
              key="noticer" 
              className="group-menu-item" 
              activeClassName="active" 
              to={`/personal_center/noticer`}
            >
              关注者
            </NavLink>
          </div>
          <Button 
            type="primary"
            onClick={() => {
              this.props.history.push('/knowledge/createNew')
            }}
          >新建知识库</Button>
        </div>
        <div className="main-content">
          <Router history={history}>
            <Switch>
              <Route key="knowledge" exact path={`/personal_center/knowledge`} component={() => <Knowledge knowledgeList={knowledgeList}/>} />
              <Route key="concern" exact path={`/personal_center/concern`} component={() => <Concern />} />
              <Route key="notice" exact path={`/personal_center/noticer`} component={() => <Noticer />} />
            </Switch>
          </Router>
        </div>
        </div>
      </div>
    )
  }
}


export default connect(state => state.userInfo)(PersonalCenter)