import React, { useState } from 'react';
import { Checkbox, Button, Dropdown, Menu, Spin, Pagination } from 'antd';
import Icon from '../Icon/Icon';
import HasNoData from '../HasNoData';
import './index.css';

export default function SharedTable(props) {
  let { 
    columns, 
    dataSource, 
    parentPath, 
    hasCheckedBox, 
    renderExtraBtn, 
    getCheckedList,
    loading = false,
    onTableChange = null
  } = props;
  const columnKeys = columns.map(item => item.key);
  const idList = dataSource.map(item => item.id);
  const [checkAll, setCheckAll] = useState(false);
  const [indeterminate, setIndeterminate] = useState(false);
  const [checkedList, setCheckedList] = useState([]);
  const [isBatch, setIsBatch] = useState(false);
  const [menuVisible, setMenuVisible] = useState(false);

  const onCheckAllChange = e => {
    setCheckAll(e.target.checked);
    if (e.target.checked) {
      setCheckedList(idList);
      setIsBatch(true);
      if (getCheckedList) {
        getCheckedList(idList);
      }
    } else {
      setCheckedList([]);
      setIsBatch(false);
    }
    setIndeterminate(false);
  }

  const selectRoot = (e, index) => {
    console.log(e, index);
  }

  const renderEditMenu = () => {
    return <Menu 
        onClick={e => selectRoot(e)}
      >
      <Menu.Item
        key="管理员"
        >
        <h3>管理员</h3>
        <p>拥有所有权限</p>
      </Menu.Item>
      <Menu.Item
        key="成员"
        >
        <h3>成员</h3>
        <p>拥有知识库管理权限</p>
      </Menu.Item>
      <Menu.Item
        key="只读成员"
        >
        <h3>只读成员</h3>
        <p>仅有阅读权限</p>
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item
        key="setting"
      >
        <a style={{color: '#3A92F9'}} className="settingBtn" href="">设置权限角色</a>
      </Menu.Item>
    </Menu>
  }

  const onSelect = checkedList => {
    setCheckedList(checkedList);
    setCheckAll(checkedList.length === idList.length);
    setIndeterminate(!!checkedList.length && checkedList.length < idList.length);
    setIsBatch(checkedList.length > 0);
    if (getCheckedList) {
      getCheckedList(checkedList);
    }
  }

  const dataListPage = (dataSource, columns, columnKeys, hasCheckedBox) => (
    dataSource.map((item, index) => (
      <div key={item.courseName} className="row-item">
        {
          columnKeys.map((key, order) => {
            if(columns[order].render) {
              return <div 
                key={key} 
                style={{width: columns[order].width}} 
                className={`column-children ${columns[order].className}`}
              >
                {columns[order].render(item, index)}
              </div>
            }
            if (Array.isArray(item[key])) {
              return <div 
                key={key} 
                style={{width: columns[order].width}} 
                className={`column-children ${columns[order].className}`}
              >
                {item[key].join(' ')}
              </div>
            }
            if (order === 0) {
              return <div 
                key={key} 
                className={`column-children ${columns[order].className}`} 
                style={{width: columns[order].width}}
              >
                {item.icon && <div className="column-children-icon">{typeof item.icon === 'function' ? item.icon() : item.icon}</div>}
                {hasCheckedBox && <Checkbox 
                    style={{marginRight: '12px'}} 
                    value={item.id}
                  />
                }
                {<a href={parentPath+item.id}>{item[key]}</a>}
              </div>
            }
            return <div 
              className={`column-children ${columns[order].className}`} 
              key={key} 
              style={{width: columns[order].width}}
            >
              {item[key]}
            </div>
          })
        }
      </div>
    ))
  )
  return (
    <div className="listContent">
      <div className="table-header">
        {
          columns.map((item, order) => {
            if (order === 0 && hasCheckedBox) {
              return <div 
                style={{width: item.width}} 
                key={item.key} 
                className={`column-item`}
              >
                <Checkbox
                  indeterminate={indeterminate}
                  onChange={onCheckAllChange}
                  checked={checkAll}
                  style={{marginRight: '12px'}}
                />
                {isBatch ? `已选择${checkedList.length}个` : item.title}
              </div>
            }

            if (!isBatch) {
              return <div 
                style={{width: item.width}} 
                key={item.key} 
                className={`column-item`}
              >
                {item.title}
              </div>
            }
          })
        }
        {
          isBatch && (!renderExtraBtn ? <div className="batch-module flex">
            <Dropdown
              overlay={() => renderEditMenu()}
              visible={menuVisible}
              onVisibleChange={(visible) => setMenuVisible(visible)}
            >
              <div>
                <span className="curser-pointer">批量调整权限</span>
                <Icon type={menuVisible ? 'iconlujing246' : 'iconlujing133'} />
              </div>
            </Dropdown>
            <Button type="link">删除</Button>
          </div> :
          renderExtraBtn())
        }
      </div>
      <div className="table-content">
        <Spin spinning={loading}>
          {
            dataSource.length === 0 ? <HasNoData /> : 
            (
              hasCheckedBox ? <Checkbox.Group
                value={checkedList}
                onChange={onSelect}
                style={{width: '100%'}}
              >
                {dataListPage(dataSource, columns, columnKeys, hasCheckedBox)}
              </Checkbox.Group> : dataListPage(dataSource, columns, columnKeys, hasCheckedBox)
            )
          }
        </Spin>
        {/* ToDoList后端没有返回total总数据量 */}
        <Pagination 
          hideOnSinglePage={true}
          onChange={onTableChange}
        />
      </div>
    </div>
  )
}