import React, { useState, useEffect } from 'react';
import { Spin } from 'antd';
import styles from './index.module.css';

export default function PlaceHolder(props) {
  const [loading, setLoading] = useState(true);
  const { title, className } = props;

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 150);
  }, [])
  return (
    <Spin spinning={loading}>
      <div className={`${styles.thumbWall} ${className}`}>
          <img className={styles.warnImg} src={require('../../assets/images/placeHolder.jpg')} alt=""/>
          <p className={styles.desc}>抱歉～，为了您更好地使用体验，研发小哥哥正在加班加点开发
            &nbsp;<span className={styles.keywords}>{title}</span>&nbsp;
            页面
          </p>
      </div>
    </Spin>
  )
}