import React, { Component } from 'react';
import { Upload, Button } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import { getBase64, beforeUpload } from '../../utils/oss';
import { uploadImg } from '../../api/upload';

export default class UploadAvatar extends Component {
  state = {
    loading: false,
  };

  handleChange = info => {
    if (info.file.status === 'uploading') {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, imageUrl =>
        this.setState({
          imageUrl,
          loading: false,
        }, () => this.props.onUpload(imageUrl)),
      );
    }
  };

  uploadToServer() {

  }

  render() {
    const { imageUrl } = this.state;
    return (
      <Upload
        name="avatar"
        showUploadList={false}
        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
        beforeUpload={beforeUpload}
        onChange={this.handleChange}
      >
        <Button>
          <UploadOutlined /> {imageUrl ? '修改头像' : '上传头像'}
        </Button>
        <br />
        {!imageUrl && <p style={{marginTop: 12}} className="desc-paragraph">点击上传按钮上传头像</p>}
      </Upload>
    );
  }
}