import React from 'react';
import { Tree } from 'antd';
import moment from 'moment';

import styles from './index.module.css';

const x = 3;
const y = 2;
const z = 1;
const gData = [];

const generateData = (_level, _preKey, _tns) => {
  const preKey = _preKey || '0';
  const tns = _tns || gData;
  const createTime = moment().format("MM-DD HH:MM");

  const children = [];
  for (let i = 0; i < x; i++) {
    const key = `${preKey}-${i}`;
    tns.push({ title: key, key, createTime, });
    if (i < y) {
      children.push(key);
    }
  }
  if (_level < 0) {
    return tns;
  }
  const level = _level - 1;
  children.forEach((key, index) => {
    tns[index].children = [];
    return generateData(level, key, tns[index].children);
  });
};
generateData(z);

 export default class TreeList extends React.Component {
  state = {
    gData: [],
  }
  componentWillReceiveProps (nextProps) {
    const { gData } = nextProps;
    if(gData.length !== this.props.gData.length) {
      this.setState({
        gData: this.initialData(gData),
      });
    }
  }
  
  initialData = (list) => {
    return list.map(item => {
      if(item.children) {
        return {
          ...item,
          children: this.initialData(item.children),
          title: this.renderTreeNode(item),
        }
      } else {
        return {
          ...item,
          title: this.renderTreeNode(item),
        }
      }
    })
  }

  renderTreeNode = (item) => {
    const {title, createTime, key} = item;
    return (
      <div className={styles.menuItem} key={key}>
        {
          <span onClick={() => this.props.onItemClick(item)} className="title">
            {title}
          </span>
        }
        <span className={styles.line}>
        </span>
        <span className={styles.affixInfo}>
          {createTime}
        </span>
      </div>
    )
  }

  onDragEnter = info => {
    // expandedKeys 需要受控时设置
    // this.setState({
    //   expandedKeys: info.expandedKeys,
    // });
  };

  onDrop = info => {
    console.log(info);
    const dropKey = info.node.props.eventKey;
    const dragKey = info.dragNode.props.eventKey;
    const dropPos = info.node.props.pos.split('-');
    const dropPosition = info.dropPosition - Number(dropPos[dropPos.length - 1]);

    const loop = (data, key, callback) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].key === key) {
          return callback(data[i], i, data);
        }
        if (data[i].children) {
          loop(data[i].children, key, callback);
        }
      }
    };
    const data = [...this.state.gData];

    // Find dragObject
    let dragObj;
    loop(data, dragKey, (item, index, arr) => {
      arr.splice(index, 1);
      dragObj = item;
    });

    if (!info.dropToGap) {
      // Drop on the content
      loop(data, dropKey, item => {
        item.children = item.children || [];
        // where to insert 示例添加到尾部，可以是随意位置
        item.children.push(dragObj);
      });
    } else if (
      (info.node.props.children || []).length > 0 && // Has children
      info.node.props.expanded && // Is expanded
      dropPosition === 1 // On the bottom gap
    ) {
      loop(data, dropKey, item => {
        item.children = item.children || [];
        // where to insert 示例添加到头部，可以是随意位置
        item.children.unshift(dragObj);
      });
    } else {
      let ar;
      let i;
      loop(data, dropKey, (item, index, arr) => {
        ar = arr;
        i = index;
      });
      if (dropPosition === -1) {
        ar.splice(i, 0, dragObj);
      } else {
        ar.splice(i + 1, 0, dragObj);
      }
    }

    this.setState({
      gData: data,
    });
  };

  render() {
    return (
      <Tree
        className={styles.draggableTree}
        // defaultExpandedKeys={this.state.expandedKeys}
        // draggable
        blockNode
        onDragEnter={this.onDragEnter}
        onDrop={this.onDrop}
        treeData={this.state.gData}
        selectable={false}
      />
    );
  }
}
