import React from 'react';
import { createFromIconfontCN } from '@ant-design/icons';

const IconFont = createFromIconfontCN({
  scriptUrl: '//at.alicdn.com/t/font_1755333_e7pnuhafyh7.js',
}) 

export default function Icon(props) {
  const { className, styles, type, onClick } = props;
  const onAction = () => {
    if (onClick) {
      onClick();
    } else {
      return null;
    }
  }
  return (
    <IconFont 
      className={className} 
      styles={{...styles}} 
      type={type} 
      onClick={() => onAction()} 
    />
  )
}