import React from 'react';
import { Row, Col } from 'antd';
import './index.css'
import ItemCard from './ItemCard';
import { connect } from 'mirrorx';

const MainGuidePage = (props) => {
  const { list, detailInfos } = props;

  return <div className="guide-page-content">
    <h1 className="title">{detailInfos.userNick || detailInfos.name}，欢迎来到鲸落</h1>
    <p className="desc">你可以点击下方的知识创作场景，开启你的第一篇鲸落文档。</p>
    <Row gutter={[30, 20]}>
      {
        list.map(item => (
          <Col key={item.id} className="gutter-row" span={8}>
            <ItemCard {...item} />
          </Col>
        ))
      }
    </Row>
  </div>
}

export default connect(state => state.userInfo)(MainGuidePage);