import React, { useState } from 'react';
import { Modal, Form, Input, DatePicker, Button } from 'antd';
import locale from 'antd/es/date-picker/locale/zh_CN';
import Icon from '../../../public/Icon/Icon';

const { RangePicker } = DatePicker;
const statusList = ['进行中', '已完成'];
const statusClassNames = ['doing', 'finish'];
const iconTypes = {
  '执行者': 'iconzu318',
  '时间': 'iconzu317',
  '描述': 'iconzu319',
  '标签': 'iconzu324',
  '备注': 'iconzu321',
  '归属': 'iconzu322',
  '提交': 'iconzu318',
}

export default function ModuleDetailModal(props) {
  const [form] = Form.useForm()

  const renderTitle = (title, status, content) => {
    return (
      <div className="flex">
        <h3>{title} · {status === 1 ? '查看任务' : content ? '编辑任务' : '新建任务'}</h3>
        <span className={`${statusClassNames[status]} tags`}>
          {statusList[status]}
        </span>
      </div>
    )
  }

  const renderLabel = (label) => {
    return (
      <div>
        <Icon className="left-icon" type={iconTypes[label]}/>
        <span>{label}</span>
      </div>
    )
  }

  const handleOk = () => {
    form.validateFields().then(values => {
      // form.resetFields();
      onChange(values);
    })
  }

  const { 
    title, 
    status, 
    visible, 
    onCancel, 
    createTime, 
    finallyDate,
    courseTitle,
    parentTitle,
    onChange,
  } = props;

  const formItemLayout = {
    labelCol: {
      xs: { span: 16 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 8 },
      sm: { span: 16 },
    },
  };

  return (
    <Modal 
      width="720px"
      visible={visible}
      onCancel={() => {
        onCancel();
      }}
      closable={false}
      destroyOnClose
      title={renderTitle(parentTitle, status, title)}
      footer={
        [
        status === 0 && <Button key="back" onClick={() => onCancel()}>
          取消
        </Button>,
        <Button key="submit" type="primary" onClick={() => handleOk()}>
          {status === 0 ? '提交' : '确定'}
        </Button>]
      }
    >
      <Form
        form={form}
        initialValues={{
          ...props,
          rangeTime: [createTime, finallyDate]
        }}
        {...formItemLayout}
      >
        <Form.Item 
          name="owner" 
          label={renderLabel('执行者')}
          rules={[{ required: true, message: '请填写执行者' }]}
        >
          <Input disabled={status == 1} />
        </Form.Item>
        <Form.Item 
          name="rangeTime" 
          label={renderLabel('时间')}
          rules={[{ required: true, message: '请填写预计完成时间' }]}
        >
          <RangePicker locale={locale} disabled={status == 1} />
        </Form.Item>
        <Form.Item 
          name="content" 
          label={renderLabel('描述')}
          rules={[{ required: true, message: '请输入任务描述，帮助成员更好地理解任务' }]}
        >
          <Input.TextArea disabled={status == 1} />
        </Form.Item>
        <Form.Item 
          name="tag" 
          label={renderLabel('标签')}
        >
          <Input disabled={status == 1} />
        </Form.Item>
        <Form.Item label={renderLabel('归属')}>
          {courseTitle}·{parentTitle}
        </Form.Item>
      </Form>
    </Modal>
  )
}
