import React, { Component } from 'react';
import SharedTable from '../../../public/SharedTable';
import SearchBar from '../../../public/SearchBar';
import Icon from '../../../public/Icon/Icon';
import { searchGroups } from '../../../api/group';
import { actions } from 'mirrorx';
import moment from 'moment';

import './index.css';

export default class Group extends Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: '团队名称',
        key: 'groupName',
        width: 300
      },
      {
        title: '简介',
        key: 'groupDesc',
        width: 360
      },
      {
        title: '团队人数',
        key: 'groupCounts',
        width: 100,
      }
    ];

    this.state = {
      columns,
      groupList: [],
    }

  }

  async componentDidMount() {
    this.fetchData(1);
  }

  fetchData = async (page = 1, keyWord = '') => {
    let { groupList } = this.state;
    const ret = await searchGroups({
      page: page,
      type: keyWord ? '1' : '2',
      selevtName: keyWord,
    });
    if (ret.data) {
      groupList = ret.data.dataList.map(item => ({
        ...item,
        id: item.groupId,
        groupName: item.groupName,
        groupDesc: item.groupInfo,
        groupCounts: item.stuNum,
        createTime: new moment(item.createTime).format('YYYY-MM-DD HH:MM:SS'),
        icon: () => <Icon className="middle-icon" type="iconzu22" />,
      }))
    };

    const userInfo = await actions.userInfo.getUserInfos();
    this.setState({
      groupList: groupList.filter(item => item.studentCode === userInfo.studentCode),
    })
  }

  onSearch = keyWords => {
    console.log(keyWords, '请求搜索接口数据');
    this.fetchData(1, keyWords);
  }

  render() {
    const { columns, groupList } = this.state;
    return <div className="group-list">
      <SearchBar 
        onClick={() => {window.location.href="#/group/createNew"}} 
        onSearch={values => this.onSearch(values)} 
        searchTitle="团队" 
        searchBtnText="新建团队" 
      />
      <SharedTable parentPath="#/group/detail/" columns={columns} dataSource={groupList} />
    </div>
  }
}