import React, { Component } from 'react';
import { Menu, Input, Button, Avatar, message } from 'antd';
import { actions } from 'mirrorx';
import { updateUserInfo } from '../../../api';

export default class GroupSetting extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedKeys: ['basicInfo'],
      userInfos: {
        name: '高黎明',
        major: '工业设计',
        code: '311617040124',
        nickName: '刘小谌ZJUE',
        desc: '打造专业知识库，增强学生与教师的信息交流，位专业学生打造更为专业的记录知识的平台',
        phone: '180741310353',
        knowledgeCount: 100,
        teamCount: 36,
        courseCount: 32,
      },
      messageList: [
        {
          type: '知识库变更',
          desc: '我所在团队的知识库发生重命名、迁移、私密性变更等操作',
          id: 'knowledgeChange',
          isOpen: true,
        }, 
        {
          type: '知识库内容更新',
          desc: '我关注的知识库里的文档、画板、表格或资源发生变化',
          id: 'knowledgeContentChange',
          isOpen: false,
        },
        {
          type: '讨论区更新',
          desc: '我上传的作品或文档的讨论区更新时',
          id: 'commentUpdate',
          isOpen: false,
        },
        {
          type: '团队变更',
          desc: '我负责的团队发生变化时',
          id: 'teamChange',
          isOpen: false,
        },
        {
          type: '新评论',
          desc: '当他人评论我的主题和文章时',
          id: 'newComment',
          isOpen: false,
        },
        {
          type: '提到我',
          desc: '当他人在评论区提到我时',
          id: 'aboutMe',
          isOpen: false,
        },
        {
          type: '有人关注我',
          desc: '当他人关注我时',
          id: 'beConcerned',
          isOpen: false,
        },
        {
          type: '有人赞赏我',
          desc: '当他人给我的文档或作品赞赏添加群落时',
          id: 'isFavoriteMe',
          isOpen: false,
        },
        {
          type: '团队邀请',
          desc: '当他人邀请我加入某团队时',
          id: 'teamInvite',
          isOpen: true,
        },
        {
          type: '团队申请',
          desc: '当他人申请加入我负责的团队时',
          id: 'applyForTeam',
          isOpen: true,
        },
        {
          type: '加入课程',
          desc: '当系统把我加入某课程时',
          id: 'joinCourse',
          isOpen: true,
        },
        {
          type: '鲸落精选推送',
          desc: '当鲸落推送包含精选文章、精选知识库等内容的期刊时',
          id: 'pushArticles',
          isOpen: true,
        },
      ]
    }
  }

  async componentDidMount() {
    const { userInfos } = this.state;
    const detailInfos = await actions.userInfo.getUserInfos();
    console.log(detailInfos)
    this.setState({
      userInfos: {
        ...userInfos,
        name: detailInfos.name,
        nickName: detailInfos.nickName,
        desc: '我是一个爱学习的人',
        phone: detailInfos.phone || "暂无",
      }
    })
  }

  onSelect = e => {
    this.setState({
      selectedKeys: [e.key]
    })
  }

  updateUserInfos = () => {
    const { userInfos } = this.state;

    updateUserInfo({
      name: userInfos.name,
      nickName: userInfos.nickName,
    }).then(res => {
      message.success(res.data);
    })
    
  }

  renderBasicInfo = () => {
    const { userInfos } = this.state;
    return (
      <div className="page-section">
        <h3>
          个人信息
        </h3>
        <div className="main-section">
          <section className="section-person flex">
            <div className="label">姓名</div>
            <div className="desc-paragraph">
              {userInfos.name}
            </div>
          </section>
          <section className="section-person flex">
            <div className="label">专业</div>
            <div className="desc-paragraph">
              {userInfos.major}
            </div>
          </section>
          <section className="section-person flex">
            <div className="label">学号</div>
            <div className="desc-paragraph">
              {userInfos.code}
            </div>
          </section>
          <section className="part">
            <div className="label">昵称</div>
            <Input 
              value={userInfos.nickName} 
              style={{width: 420, borderRadius: '4px'}} 
              onChange={(e) => {
                const { userInfos } = this.state;

                this.setState({
                  userInfos: {
                    ...userInfos,
                    nickName: e.target.value,
                  }
                })
              }}
            />
          </section>
          <section className="part">
            <div className="label">简介</div>
            <Input.TextArea 
              value={userInfos.desc} 
              style={{width: 420, borderRadius: '4px'}} 
              onChange={(e) => {
                const { userInfos } = this.state;

                this.setState({
                  userInfos: {
                    ...userInfos,
                    desc: e.target.value,
                  }
                })
              }}
            />
          </section>
          <section className="part">
            <div className="label">头像</div>
            <div className="avatar">
              <div className="left-area">
                <Avatar size={64} />
                <div className="addBtn">
                  <Button disabled={true}>上传头像</Button>
                  <p className="desc-paragraph">
                    点击上传按钮上传头像
                  </p>
                </div>
              </div>
              <Button onClick={() => this.updateUserInfos()} className="updateBtn">更新</Button>
            </div>
          </section>
        </div>
      </div>
    )
  }

  renderAccountManager = () => {
    const { userInfos } = this.state;
    return (
      <>
        <div className="page-section accountManager">
          <h3>
            账户管理
          </h3>
          <div className="main-section">
            <div className="card-item flex border">
              <dl>
                <dt>手机号</dt>
                <dd className="desc-paragraph">{userInfos.phone.replace(userInfos.phone.slice(3, 8), '****')}</dd>
              </dl>
              <span className="curser-pointer">更改</span>
            </div>
            <div className="card-item flex border">
              <dl>
                <dt>账号密码</dt>
                <dd className="desc-paragraph">
                  已设置，可通过账号密码登录
                </dd>
              </dl>
              <span className="curser-pointer">更改</span>
            </div>
          </div>
        </div>
        <div className="page-section">
          <h3>
            删除账户
          </h3>
          <div className="main-section account-section">
            <p className="desc-paragraph">
              你的帐户拥有 {userInfos.knowledgeCount} 个人知识库，加入 {userInfos.teamCount} 个团队，加入 {userInfos.courseCount} 个课程
            </p>
            <p className="desc-paragraph">
              操作删除帐户之前，须先删除帐户下所有个人知识库，退出或转让团队。
            </p>
            <Button disabled={true} type="danger">删除</Button>
          </div>
        </div>
      </>
    )
  }

  onChangeItem = (item, index) => {
    const { messageList } = this.state;

    messageList[index].isOpen = !item.isOpen;

    this.setState({
      messageList,
    })
  }

  renderMessageInfo = () => {
    const { messageList } = this.state;
    return (
      <>
        <div className="page-section">
          <h3>
            消息通知设置
          </h3>
          <div className="main-section">
            {
              messageList.map((item, index) => {
                return <div className="card-item flex" style={{marginBottom: '21px', borderBottom: '1px solid #e5e5e5'}}>
                  <dl>
                    <dt>
                      {item.type}
                    </dt>
                    <dd className="desc-paragraph">
                      {item.desc}
                    </dd>
                  </dl>
                  {
                    item.isOpen ? 
                      <Button onClick={() => this.onChangeItem(item, index)}>已开启</Button> :
                      <Button onClick={() => this.onChangeItem(item, index)} className="defaultBtn">开启</Button>
                  }
                </div>
              })
            }
          </div>
        </div>
      </>
    )
  }

  render() {
    const { selectedKeys } = this.state;

    return (
      <div className="group-setting" style={{marginTop: '32px'}}>
        <div className="side-tab">
          <Menu onSelect={this.onSelect} selectedKeys={selectedKeys} className="menu" mode="inline">
            <Menu.Item key="basicInfo" className="menu-item">
              个人信息
            </Menu.Item>
            <Menu.Item key="accountManager" className="menu-item">
              账户管理
            </Menu.Item>
            <Menu.Item key="messageInfo" className="menu-item">
              消息通知
            </Menu.Item>
          </Menu>
        </div>
        <div className="right-content" style={{margin: 0,}}>
          {
            selectedKeys[0] === 'basicInfo' && this.renderBasicInfo()
          }
          {
            selectedKeys[0] === 'accountManager' && this.renderAccountManager()
          }
          {
            selectedKeys[0] === 'messageInfo' && this.renderMessageInfo()
          }
        </div>
      </div>
    )
  }
}