import React, { Component } from 'react';
import PopPanel from '../PopPanel';
import { Form, Input } from 'antd';

class AddEntryModal extends Component {
  state = {
    text: undefined,
    href: undefined,
  }

  onChangeItem = (type, value) => {
    this.setState({
      [type]: value,
    });
  }

  beforOneSure = () => {
    const { text, href } = this.state;
    const { onSure } = this.props;

    if (text.trim() === '' || href.trim() === '') {
      return;
    }

    onSure({text, href});

  }

  render() {
    const { text, href } = this.state;
    return (
      <PopPanel
        title="添加快捷入口"
        onOkCallBack={() => this.beforOneSure()}
      >
        <Form 
          layout="vertical"
        >
          <Form.Item label="标题">
            <Input value={text} onChange={(e) => this.onChangeItem('text', e.target.value)} placeholder="输入标题，或关键词搜索我的知识库、团队、文档" />
          </Form.Item>
          <Form.Item label="对应网址">
            <Input value={href} onChange={(e) => this.onChangeItem('href', e.target.value)} placeholder="输入团队，知识库，文档等地址" />
          </Form.Item>
        </Form>
      </PopPanel>
    )
  }
}

export default PopPanel.wrapper(AddEntryModal);