import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import Icon from '../Icon/Icon';
import './SideNavs.css'

export default function SideNav(props) {
  const { parentPath } = props;
  if(parentPath === '/dashboard') {
    window.location.hash = '/dashboard/main'
  }

  let routeList = [
    {
      key: '/dashboard/main',
      index: 0,
      title: '工作台',
      icon: 'iconzu70',
      icons: ['iconzu70', 'iconzu31', 'iconzu24'],
      isActive: false,
    },
    {
      key: '/dashboard/course',
      index: 1,
      title: '课程',
      icon: 'iconzu8',
      icons: ['iconzu8', 'iconzu30', 'iconzu23'],
      isActive: false,
    },
    {
      key: '/dashboard/group',
      index: 2,
      title: '团队',
      icon: 'iconzu10',
      icons: ['iconzu10', 'iconzu29', 'iconzu22'],
      isActive: false,
    },
    {
      key: '/dashboard/knowledges',
      index: 3,
      title: '知识库',
      icon: 'iconzu55',
      icons: ['iconzu55', 'iconlujing297', 'iconzu112'],
      isActive: false,
    },
    {
      key: '/dashboard/docs',
      index: 4,
      title: '文档',
      icon: 'iconlujing83',
      icons: ['iconlujing83', 'iconlujing51', 'iconlujing50'],
      isActive: false,
    },
    {
      key: '/dashboard/products',
      index: 5,
      title: '作品',
      icon: 'iconzu242',
      icons: ['iconzu242', 'iconzu416', 'iconzu245'],
      isActive: false,
    },
    {
      key: '/dashboard/concerns',
      index: 6,
      title: '关注',
      icon: 'iconlujing22',
      icons: ['iconlujing22', 'iconlujing52', 'iconlujing48'],
      isActive: false,
    },
    {
      key: '/dashboard/collections',
      index: 7,
      title: '收藏',
      icon: 'iconlujing23',
      icons: ['iconlujing23', 'iconlujing53', 'iconlujing47'],
      isActive: false,
    },
    {
      key: '/dashboard/latestViews',
      index: 8,
      title: '最近浏览',
      icon: 'iconzu415',
      icons: ['iconzu415', 'iconlujing298', 'iconzu414'],
      isActive: false,
    },
    {
      key: '/dashboard/recycleBin',
      index: 9,
      title: '回收站',
      icon: 'iconzu12',
      icons: ['iconzu12', 'iconzu28', 'iconzu62'],
      isActive: false,
    },
  ];

  const targetRoute = routeList.filter(item => item.key === parentPath)[0];
  if (targetRoute) {
    routeList[targetRoute.index].isActive = true;
    routeList[targetRoute.index].icon = routeList[targetRoute.index].icons[2];
  } else {
    routeList[0].isActive = true;
    routeList[0].icon = routeList[0].icons[2];
  }
  
  const [routes, setRoutes] = useState(routeList);
  

  const onItemMouseEnter = (route, index) => {
    let routeArray = [...routeList];
    routeArray[index].icon = route.icons[1];
    setRoutes(routeArray);
  }
  
  const onItemMouseLeave = (route, index) => {
    let routeArray = [...routeList];
    routes[index].icon = route.icons[route.isActive ? 2 : 0];
    setRoutes(routeArray);
  }

  const onItemClick = (route, index) => {
    let routeArray = routeList.map((route, order) => ({
      ...route,
      icon: order === index ? route.icons[1] : route.icons[0],
      isActive: false,
    }));
    routes[index].isActive = true;
    routes[index].icon = route.icons[2];
    setRoutes(routeArray);
  }

  return (
    <nav className="sider-nav">
      {
        routes.map((route, index) => {
          return <NavLink 
            to={route.key}
            key={route.key}
            className="sider-nav-item"
            activeClassName="active"
            onMouseEnter={() => onItemMouseEnter(route, index)}
            onMouseLeave={() => onItemMouseLeave(route, index)}
            onClick={() => onItemClick(route, index)}
          >
            <Icon className="side-icon-item" type={route.icon} />
            <span>{route.title}</span>
          </NavLink>
        })
      }
    </nav>
  )
}