import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Modal } from 'antd';

class PopPanel extends Component {
  state = {
    visible: true,
  }

  close = () => {
    console.log('111')
    if (this.props.onBeforeClose) {
      this.props.onBeforeClose();
    };

    this.destroy();
  }

  destroy = () => {
    this.setState({
      visible: false,
    })
  }

  submit = () => {
    if(this.props.onOkCallBack) {
      this.props.onOkCallBack();
    };
    this.destroy();
  }

  render() {
    const { visible } = this.state;
    return <Modal
      visible={visible}
      onCancel={() => this.close()}
      onOk={this.submit}
      {...this.props} //ToDoList 后期禁止这种写法
      okText="确认"
      cancelText="取消"
    >
      {
        this.props.children
      }
    </Modal>
  }
}

PopPanel.wrapper = function(Component) {
  Component.show = (props) => {
    const container = document.createElement('div');
    document.body.appendChild(container);
    ReactDOM.render(<Component {...props}/>, container);
  }

  return Component;
}

export default PopPanel;