import React, { Component } from 'react';
import ColumnItem from '../../components/TaskBoardComponents/ColumnItem/ColumnItem';
import moment from 'moment';
import './index.css';
import { Input } from 'antd';

export default class TaskBoard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: localStorage.getItem('knowledgeId'),
      name: '交互设计',
      taskModules: [
        {
          title: '待处理',
          id: 'todo',
          children: [],
        },
        {
          title: '理论知识学习',
          id: 'knowledge',
          children: [],
        },
        {
          title: '市场调研',
          id: 'market',
          children: [],
        },
        {
          title: '设计方案迭代',
          id: 'design',
          children: [],
        },
        {
          title: '研发中',
          id: 'work',
          children: [],
        },
        {
          title: '审核',
          id: 'check',
          children: [],
        },
        {
          title: '定稿',
          id: 'done',
          children: [],
        },
        {
          title: '已完成',
          id: 'success',
          children: [],
        }
      ]
    }
  }

  componentDidMount() {
    const { taskModules } = this.state;
    const date = moment().subtract(7, 'day');
    const finallyDate = moment().subtract(-14, 'day');
    const types = ['ppt', 'wps', 'pdf', 'sheet'];
    taskModules.map((item, index) => {
      const sum = parseInt(Math.random() * taskModules.length + 4);
      for (let i = 0; i < sum; i++) {
        item.children.push({
          creator: '高黎明',
          owner: '丁义秋',
          createTime: date,
          finallyDate,
          status: (i + 1) % 2,
          tag: '草图方案',
          title: '市场调研一',
          content: '收集市场调研资料,整理竞品分享文档，沉淀鲸落文档',
          level: parseInt(Math.random() * 4),
          accessory: ((i + 1) % 2 == 1) && {
            title: `交互数据实地访谈数据总结.${types[parseInt(Math.random() * 4)]}`,
            size: '1.25 MB'
          }
        })
      }
    })

    this.setState({
      taskModules,
    })
  }

  render() {
    const { id, name, taskModules } = this.state;
    return (
      <div className="task-board flex">
        <div className="header flex">
          <h3>
            <img className="logo" width={48} src={require('../../assets/images/logo.png')} alt=""/>
            <span>
              <a className="backBtn" href={`#/course/detail/${id}`}>{name}</a> / 任务看板
            </span>
          </h3>
          <Input.Search className="search-bar" placeholder="请输入任务名称或者执行人名称进行搜索" />
        </div>
        <div className="main-content">
          {
            taskModules.map(item => <ColumnItem {...item} />)
          }
        </div>
      </div>
    )
  }
}