import React, { Component } from 'react';
import E from 'wangeditor';
import editorMenu from '../../../assets/js/editorMenu';
import DialogModal from '../../../public/DialogModal/DialogModal';
import { LeftOutlined } from '@ant-design/icons';

import styles from './index.module.css';

export default class EditDocDetail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      html: undefined,
    }
  }
  
  componentDidMount() {
    const editor = new E(document.getElementById('toolBar'), document.getElementById('editor'));
    
    editor.customConfig.onchange = html => {
      this.setState({
        html,
      });
      new editorMenu({
        contentEl: 'editor',
        catalogEl: `menu`,
        selector: ['h1', 'h2', 'h3', 'h4']
      });
    };
    editor.create();
    this.warnInfo();
  }

  warnInfo() {
    DialogModal.show({
      title: '温馨提示',
      isOnlyOk: true,
      content: '富文本编辑正在开发中，您现在看到的是demo版本，暂时无法支持上传下载数据，有什么问题可以联系高黎明（QQ：3476179536）',
      onOk: () => {
        console.log('111')
      }
    })
  }
  render() {
    return (
      <div className={styles.wrap}>
        <div className={styles.pageHeader}>
          <a onClick={() => {this.props.history.push('/dashboard/main')}} className={styles.goBack}><LeftOutlined />返回上一级</a> / 富文本编辑器
        </div>
        <div className={styles.toolBarWrap}>
          <div id="toolBar" className={styles.toolBar}></div> 
        </div>
        <div className={styles.content}>
          <div id="editor" className={styles.mainContent}>
          </div>
          <div  className={styles.tree}> 
            <h3>大纲目录</h3>
            <div id="menu">
            </div>
          </div>
        </div>
      </div>
    )
  }
}