import React, { Component } from 'react';
import { Button } from 'antd';


export default class Noticer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      noticerList: [],
      currentPage: 1,
      max_length: 10,
      totalItems: 30,
    }
  }

  componentDidMount() {
    this.fetchData();
  }

  fetchData = () => {
    const { noticerList } = this.state;

    for (let i = 0; i < 10; i++) {
      noticerList.push({
        id: `concern_${i}`,
        name: '周小瑜SAMA',
        desc: '可见范围为“互联网可见”知识库默认开启，取消勾选后所有用户都无法评论。',
        avatarImg: null,
      })
    };

    this.setState({
      noticerList,
    });
  }

  loadMore = () => {
    this.fetchData();
    let { max_length } = this.state;
    max_length += 10;

    this.setState({
      max_length,
    });

  }

  render() {
    const { noticerList, max_length, totalItems } = this.state;
    return (
      <div className="concern">
        {
          noticerList.map((item, index) => {
            if (index > max_length - 1) return ;
            return (
              <div className="concern-item flex" key={item.id}>
                <div className="avatar">
                  {item.avatarImg ? 
                    <img src={item.avatarImg} /> : 
                    <img src={require('../../../assets/images/concern_avatar.png')} />
                  }
                </div>
                <div className="user-info">
                  <h2>{item.name}</h2>
                </div>
                <Button>关注了你</Button>
              </div>
            )
          })
        }
        {
          totalItems > max_length && (
            <Button onClick={() => this.loadMore()} className="load-more">加载更多</Button>
          )
        }
      </div>
    )
  }
}