import React, { Component } from 'react';
import SharedTable from '../../../../public/SharedTable';
import SearchBar from '../../../../public/SearchBar';
import { Dropdown, Menu } from 'antd'; 
import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';

import './index.css';

export default class GroupMember extends Component {
  constructor(props) {
    super(props);
    // ToDoList 后端接口问
    const columns = [
      {
        title: '昵称',
        key: 'name',
        width: 240,
      },
      {
        title: '学号',
        key: 'studentCode',
        width: 320,
        render: item => <p className="paragraph">{item.id}</p>
      },
      {
        title: '角色',
        key: 'memberRole',
        width: 320,
        render: (item, index) => {
          if (index === 0) {
            return <div className="desc-paragraph">自己</div>
          }
          return <Dropdown 
            overlay={this.renderMenu(index)}
          >
            <span className="role">
              <p>{item.memberRole}</p>
              <CaretDownOutlined style={{color: '#999'}} />
            </span>
          </Dropdown>
        }
      },
      {
        title: '操作',
        key: 'order',
        width: 80,
        render: (item, index) => {
          if (item.order === 0) {
            return <a onClick={() => this.quit(item)}>退出</a>
          } 
          return <a onClick={() => this.delete(item)}>删除</a>
        }
      }
    ]

    this.state = {
      columns,
      dataSource: [],
    }
  }
  
  componentDidMount() {
    if (this.props.memberList.length) {
      this.initPage(this.props.memberList);
    }
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.memberList.length !== this.props.memberList.length) {
      this.initPage(nextProps.memberList);
    };

  }

  initPage(memberList) {
    memberList = memberList.map((item, index) => ({
      ...item,
      memberRole: index === 0 ? '自己' : '成员',
      id: item.studentId,
    }))
    console.log(memberList, 'memberList')

    this.setState({
      dataSource: memberList,
    })
  }
  
  renderMenu = index => {
    return <Menu 
      onClick={e => this.selectRoot(e, index)}
    >
      <Menu.Item
        key="管理员"
        >
        <strong>管理员</strong>
        <p>拥有所有权限</p>
      </Menu.Item>
      <Menu.Item
        key="成员"
        >
        <strong>成员</strong>
        <p>拥有知识库管理权限</p>
      </Menu.Item>
      <Menu.Item
        key="只读成员"
        >
        <strong>只读成员</strong>
        <p>仅有阅读权限</p>
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item
        key="setting"
      >
        <a style={{color: '#3A92F9'}} className="settingBtn" href="">设置权限角色</a>
      </Menu.Item>
    </Menu>
  }
  
  selectRoot = (values, index) => {
    const { key } = values;
    const { dataSource } = this.state;
    dataSource[index].memberRole = key;
    this.setState({
      dataSource,
    })
  }

  quit = item => {
    console.log(item, '退出团队');
  }

  delete = item => {
    console.log(item, '删除成员')
  }


  render() {
    const { columns, dataSource } = this.state;
    return <div className="knowledge-list">
      <SearchBar searchTitle="成员管理" placeholderText="搜索成员" searchBtnText="添加成员" />
      <SharedTable hasCheckedBox dataSource={dataSource} hasCheckedBox={true} columns={columns} /> 
    </div>
  }
}