import mirror, { actions } from 'mirrorx';

export default mirror.model({
  name: 'knowledge',
  initialState: {
    knowledgeList: [],
  },
  reducers: {
    save(state, payload) {
      return {
        ...state,
        ...payload,
      }
    }
  },
  effects: {
    async getKnowledgeList(options, getState) {
      const { knowledgeList } = getState().knowledge;

      if (knowledgeList.length > 0) {
        return knowledgeList;
      } else {
        for (let i = 0; i < 9; i++) {
          knowledgeList.push({
            id: `3sjdfash${i}`,
            icon: undefined,
            knowledgeName: `交通工具设计-毕设组_${i}`,
            belong: '替狗小队',
            knowledgeDesc: '根据需求对车辆的造型进行推敲创新',
            behavior: '高黎明',
            updatedTime: '14: 23'
          });
        };

        actions.knowledge.save({
          knowledgeList,
        });

        return knowledgeList;
      }
    }
  }
})