import React, { Component } from 'react';
import { Router, Route, Switch} from 'react-router-dom';
import { createHashHistory } from 'history';
import Guide from '../pages/guide';
import Home from '../pages/Home';
import TaskTable from '../pages/TaskBoard';
import DialogModal from '../public/DialogModal/DialogModal';
import EditDocDetail from '../pages/docs/EditDocDetail';
import { connect, actions } from 'mirrorx';
import { set, get } from '../utils';
import styles from './index.module.css';

const history = createHashHistory();

class RouterConfig extends Component {
  async componentDidMount() {
    if(!localStorage.getItem('token')) {
      window.location.href = "#/login";
    };
    if (window.location.hash.includes('login') || window.location.hash.includes('changePassword')) return; 
    await this.isNewUser();
    this.startLoop();
  }

  isNewUser = async() => {
    if(get('messageForDay')) return;
    const { detailInfos } = this.props;
    if (!detailInfos.id) {
      const res = await actions.userInfo.getUserInfos();
      if(res && !res.groupId) {
        DialogModal.show({
          title: '温馨提示',
          content: this.renderGuideWord(),
          width: 800,
          isOnlyOk: true,
          onOk: () => {
            set('messageForDay', true, 24 * 60);
          }
        })
      }
    }
  }

  renderGuideWord = () => {
    return (
      <>
        <p className={styles.para}>
          欢迎您第一次来到<strong>鲸落</strong>，鲸落是一个能够跨多端设备使用的<strong>云端的知识管理工具</strong>，帮助您建立完整知识体系！
          不仅如此，您还可以使用鲸落进行各类团队合作，常见团队合作场景有<strong>课程团队</strong>，<strong>互联网+比赛</strong>等！
        </p>
        <p className={styles.para}>不得不告诉您，为了您更好地使用体验，研发小哥正在奋笔疾书开发新功能，目前支持的功能只有<strong>课程，团队，知识库</strong>，后期我们会不断进行产品迭代升级</p>
        <p className={styles.para}>如果您在产品使用过程中，有任何不舒适的地方，请及时联系相关人员<strong>QQ号：3476179536</strong>，秒回！</p>
      </>
    )
  }

  startLoop = () => {
    if(!(typeof localStorage.getItem('token') === 'string')) {
      this.warnDialogModal();
    }
    const timer = setInterval(() => {
      const isLogin = (typeof localStorage.getItem('token') === 'string');
      if (!isLogin) {
        this.warnDialogModal();
        clearTimeout(timer);
        return;
      };
    }, 5 * 60 * 1000);
  }

  warnDialogModal = () => {
    setTimeout(() => {
      DialogModal.show({
        title: '温馨提示',
        content: '为了更好体验产品，请先登录！',
        isOnlyOk: true,
        onOk: () => {
          window.location.href="#/login";
        },
      });
    }, 3 * 1000);
  }

  render() {
    return(
      <Router history={history}>
        <Switch>
          <Route path="/login" exact component={Guide} />
          <Route path="/changePassword" exact component={Guide} />
          <Route path="/bindPhone" exact component={Guide} />
          <Route path="/taskTable" exact component={TaskTable} />
          <Route path="/docDetail/edit/:id" exact component={EditDocDetail} />
          <Route path="/" component={Home} />
        </Switch>
      </Router>
    )
  }
}

export default connect(state => state.userInfo)(RouterConfig);