import React from 'react';
import { Layout, Button } from 'antd';
import HeaderNav from '../Headers/HeaderNav';
import SiderNav from '../SideNavs/SideNavs';
import { getAllUsers, updateUserInfo } from '../../api';
import './index.css'
import { connect } from 'mirrorx';
const { Header, Content, Footer, Sider } = Layout;

function AppLayout(props) {
  return(
    <div className="layout-wrapper">
      <Layout style={{background: '#fff'}}>
        <Header className="layout-header">
          <HeaderNav  history={props.children.props.history}/>
        </Header>
        <Layout className="layout-contentWrap">
          {props.isShowSider && (
            <Sider width={232} className="layout-sider">
              <SiderNav parentPath={props.searchType} />
            </Sider>
          )}
          <Content style={{paddingTop: props.isShowSider ? '32px' : 0}} className="layout-content">{props.children}</Content>
        </Layout>
        {/* <Footer className="layout-footer">底部网站信息</Footer> */}
      </Layout>
    </div>
  )
}

export default connect(state => state.userInfo)(AppLayout)