import React, { Component } from 'react';
import PopPanel from '../PopPanel';
import { Form, Input } from 'antd';

class TaskEditModal extends Component {
  state = {
    title: undefined,
    desc: undefined,
  }

  onChangeItem = (type, value) => {
    this.setState({
      [type]: value,
    });
  }

  beforOneSure = () => {
    const { title, desc } = this.state;
    const { onSure } = this.props;

    if (title.trim() === '' || desc.trim() === '') {
      return;
    }

    onSure({title, desc});

  }

  render() {
    const { title, desc } = this.state;
    return (
      <PopPanel
        title="公告栏编辑"
        onOkCallBack={() => this.beforOneSure()}
      >
        <Form 
          layout="vertical"
        >
          <Form.Item label="任务标题">
            <Input value={title} onChange={(e) => this.onChangeItem('title', e.target.value)} placeholder="今天的任务" />
          </Form.Item>
          <Form.Item label="任务简述">
            <Input.TextArea value={desc} onChange={(e) => this.onChangeItem('desc', e.target.value)} placeholder="任务简述" />
          </Form.Item>
        </Form>
      </PopPanel>
    )
  }
}

export default PopPanel.wrapper(TaskEditModal);