import React, { Component } from 'react';
import Icon from '../../../public/Icon/Icon';
import ModuleDetailModal from '../ModuleDetailModal/ModuleDetailModal';

import { Button } from 'antd';

import './index.css';
import { useState } from 'react';

const statusList = ['待提交', '已提交'];
const statusClassNames = ['todo', 'submit'];
const levelList = ['低', '中', '高', '紧急'];
const typeList = [
  {
    type: 'wps',
    icon: 'iconzu382',
  },
  {
    type: 'pdf',
    icon: 'iconzu385',
  },
  {
    type: 'sheet',
    icon: 'iconzu384',
  },
  {
    type: 'ppt',
    icon: 'iconzu383',
  }
];
const iconList = ['iconzu382', 'iconzu385', 'iconzu384', 'iconzu383'];

export default function ModuleItem (props) {
  const [visible, setVisible] = useState(false);
  const [detailInfo, setDetailInfo] = useState(props)

  const { creator, owner, createTime, title, content, finallyDate, status, level, tag, accessory } = detailInfo;
  const type = accessory && typeList.filter(item => item.type === accessory.title.split('.')[1])[0];

  const onChange = values => {
    const { owner, content, rangeTime, tag } = values;
    setDetailInfo({
      ...detailInfo,
      owner,
      content,
      createTime: rangeTime[0],
      finallyDate: rangeTime[1],
      tag, 
    });
    setVisible(false);
  }
  return (
    <div className="module-item">
      <h3 className="title flex">
        <div className="left">
          <img src={require('../../../assets/images/ed.png')} alt=""/>
          <span className="name">{owner}</span>
        </div>
        <span className={`tag ${statusClassNames[status]}`}>
          {statusList[status]}
        </span>
      </h3>
      <h2 onClick={() => {
          setVisible(true);
        }}
        className={`content ${status === 1 && 'desc-paragraph'}`}
      >{content}</h2>
      <ul>
        {/* <li>创建者：{creator}</li> */}
        <li>优先级：{levelList[level]}</li>
        {/* <li>创建时间：{createTime}</li> */}
        {
          accessory ? (
            <li className="flex doc-area">
              <Icon className="doc-type" type={type.icon} />
              <dl className="docs">
                <dt>{accessory.title}</dt>
                <dd>{accessory.size}</dd>
              </dl>
              <Button className="downloadBtn" type="primary">下载</Button>
            </li>
          ) : (
            <li className="flex">
              <div className="leftArea">
                <Icon className="left-icon" type="iconzu323" />
                <span className="special-text">{finallyDate.format('M-DD HH:mm')}&nbsp;</span>
                之前
              </div>
              <div className="rightArea">
                <Icon className="left-icon" type="iconzu324"/>
                {tag}
              </div>
            </li>
          )
        }
      </ul>
      
      {visible && <ModuleDetailModal 
        {...props}
        visible={visible}
        onCancel={() => setVisible(false)}
        onChange={(values) => onChange(values)}
      />}
    </div>
  )
}