import React, { useEffect } from 'react';
import Dashboard from '../dashboard';
import ThumbtackWall from '../thumbtack_wall';
import AppLayout from '../../public/AppLayouts/AppLayout';
import PersonalCenter from '../personal_center';
import News from '../news';
import Feedback from '../feedback';
import HelpCenter from '../help_center';
import CourseDetail from '../../components/DashboardMainContent/Course/CourseDetail';
import GroupCreateNew from '../../components/DashboardMainContent/Group/GroupCreateNew';
import GroupDetail from '../../components/DashboardMainContent/Group/GroupDetail/GroupDetail';
import KnowledgeDetail from '../../components/DashboardMainContent/Knowledges/KnowledgeDetail/KnowledgeDetail';
import KnowledgeCreateNew from '../../components/DashboardMainContent/Knowledges/KnowledgeCreateNew/KnowledgeCreateNew';
import KnowledgeManager from '../../components/DashboardMainContent/Knowledges/KnowledgeManager/KnowledgeManager';
import PersonalInfoEdit from '../../pages/personal_center/PerosnalInfoEdit/PersonalInfoEdit';
import { Router, Switch, Route, Redirect } from 'react-router-dom';
import { createHashHistory } from 'history';
import { connect, actions } from 'mirrorx';
const history = createHashHistory();

function Home(props) {
  const searchType = props.history.location.pathname;
  if (searchType === '/') props.history.replace('/dashboard');
  const isShowSider = searchType.includes('dashboard') && !searchType.includes('detail');

  useEffect(() => {
    actions.userInfo.getUserInfos();
    setTimeout(() => {
      if (!localStorage.getItem('token')) {
        
      }
    }, 1000)
  });

  return (
    <AppLayout isShowSider={isShowSider} searchType={searchType}>
      <Router history={history}>
        <Switch>
          <Route path="/" exact component={() => <Redirect to="/dashboard" />} />
          <Route path="/dashboard" component={(props) => <Dashboard {...props} />} />
          <Route path="/group/createNew" component={() => <GroupCreateNew />} />
          <Route exact path="/course/detail/:id" component={CourseDetail} />
          <Route path="/group/detail/:id" component={GroupDetail} />
          <Route path="/knowledge/detail/:id" component={(props) => <KnowledgeDetail {...props} />} />
          <Route path="/knowledge/createNew/:groupId?" component={(props) => <KnowledgeCreateNew {...props}/>} />
          <Route path="/knowledge/manager/:type/:id" component={(props) => <KnowledgeManager {...props}/>} />
          <Route exact path="/thumbtackWall" component={() => <ThumbtackWall />} />
          <Route exact path="/news" component={() => <News />} />
          <Route exact path="/helpCenter" component={() => <HelpCenter />} />
          <Route exact path="/feedback" component={() => <Feedback />} />
          <Route path="/personal_center" component={PersonalCenter} />
          <Route exact path="/personalInfo_edit" component={PersonalInfoEdit} />
        </Switch>
      </Router>
    </AppLayout>
  )
}

export default connect(state => ({...state}))(Home)