export const set = (key, value, expired) => {
  window.localStorage.setItem(key, value);
  window.localStorage.setItem(`${key}_expires_`, Date.now() + 1000*60*expired);
}

export const get = (key) => {
  const date = Date.now();
  const expiredTime = JSON.parse(window.localStorage.getItem(`${key}_expires_`));
  if (expiredTime && date > expiredTime) {
    return false;
  };
  return JSON.parse(window.localStorage.getItem(key)) || false;
}