import React, { Component } from 'react';
import PopPanel from '../PopPanel';
import { actions } from 'mirrorx';
import { Form, Select, Spin } from 'antd';
import debounce from 'lodash/debounce';
import Icon from '../Icon/Icon';
import './index.css';

const { Option } = Select;

class StudentSelectModal extends Component {
  state = {
    students: [],
    targetStudent: {},
    fetching: false,
  }

  constructor(props) {
    super(props);
    this.fetchUser = debounce(this.fetchUser, 800);
  }
  
  componentDidMount = async () => {
    const students = await actions.userInfo.getUsers();
    const userInfos = await actions.userInfo.getUserInfos();
    this.setState({
      students: students.filter(item => item.classId === userInfos.classId),
    });
  }

  onSelectItem = (values) => {
    const { students } = this.state;
    const targetStudent = students.find(item => item.studentId === values);
    this.setState({
      targetStudent,
    });
  }

  fetchUser = async (value) => {
    this.setState({ students: [], fetching: true });
    let students = await actions.userInfo.getUsers();
    const userInfos = await actions.userInfo.getUserInfos();
    students = students.filter(item => item.classId === userInfos.classId);
    if (typeof (value - 0) === 'number' && !isNaN(value)) {
      students = students.filter(student => student.studentCode.includes(value));
    } else {
      students = students.filter(student => student.name.includes(value));
    }

    this.setState({
      students,
      fetching: false,
    })
  }

  renderWarning = () => {
    return (
      <div className="warn-content flex">
        <Icon className="icon" type="iconzujian63" />
        <div>
          很抱歉，当前模式下只能选择同班级的同学，如果有跨班组队的需要请及时联系<strong>高黎明（QQ号：3476179536）</strong>  
        </div>
      </div>
    )
  }

  render() {
    const { students, targetStudent, fetching } = this.state;
    const { onSelect } = this.props;
    return (
      <PopPanel
        title="新增成员"
        onOkCallBack={() => onSelect(targetStudent)}
      >
        {
          this.renderWarning()
        }
        <Form 
          className="addForm"
        >
          <Form.Item label="请选择成员">
            <Select 
              showSearch
              placeholder="请选择团队成员"
              onChange={values => this.onSelectItem(values)}
              onSearch={this.fetchUser}
              notFoundContent={fetching ? <Spin size="small" /> : null}
            >
              {
                students.map(item => (
                  <Option 
                    key={item.studentId} 
                    value={item.studentId}
                  >
                    {`${item.name} (${item.studentCode})`}
                  </Option>
                ))
              }
            </Select>
          </Form.Item>
        </Form>
      </PopPanel>
    )
  }
}

export default PopPanel.wrapper(StudentSelectModal);