import React, { useState } from 'react';
import { Input, Button } from 'antd';
import './index.css';

const { Search } = Input;

export default function SearchBar(props) {
  const { searchTitle, searchBtnText, onSearch, placeholderText, noActions } = props;
  const [keyword, setKeyword] = useState(null);
  return (
    <div className="listHeader">
      <p className="title noBorder">{searchTitle}</p>
      <div className="rightArea">
        <Search 
          placeholder={placeholderText ? placeholderText : '搜索'}
          style={{marginRight: '21px'}}
          onChange={e => {setKeyword(e.target.value)}}
          onSearch={onSearch}
          allowClear
          value={keyword}
        />
        {
          !noActions && 
          <Button onClick={() => props.onClick(keyword)} type="primary" style={{lineHeight: 1, height: 36}}>
            {searchBtnText}
          </Button>
        }
      </div>
    </div>
  )
}