import React from 'react';
import Icon, { createFromIconfontCN } from '@ant-design/icons';
import { Dropdown, Menu } from 'antd';

import './index.css';

const IconFont = createFromIconfontCN({
  scriptUrl: '//at.alicdn.com/t/font_1755333_zinaf1xwqgh.js',
});


export default function KnowledgeItem(props) {
  const { knowledgeName, desc, updatedDocs } = props;
  const onSelectItem = values => {
    const { key } = values;
    switch (key) {
      case 'setting':
        console.log('setting', '进入到设置页面');
        break;
      case 'hidden':
        console.log('hidden', '隐藏首页显示');
        break;
      default:
        break;
    }
  };

  const menu = () => (
    <Menu onClick={onSelectItem}>
      <Menu.Item key="hidden">不在首页显示</Menu.Item>
      <Menu.Item key="setting">设置</Menu.Item>
    </Menu>
  )

  return(
    <div className="knowledge-item-card">
      <div className="title-section">
        <div className="title-text">
          <IconFont className="icon_book" type="iconzu112" />
          <div className="title">
            <h3>{knowledgeName} <IconFont type="iconlujing40" /></h3>
            <p className="desc-paragraph">
              {desc}
            </p>
          </div>
        </div>
        <div className="more-btn">
          <Dropdown overlay={menu}>
            <IconFont className="more-btn-icon" type="iconzu329" />
          </Dropdown>
        </div>
      </div>
      <ul className="card-content">
        {
          updatedDocs.map(item => <li key={item.id}>
            <span>{item.title}</span>
            <span>{item.updatedTime}</span>
          </li>)
        }
      </ul>
    </div>
  )
}