import React from 'react';
import { Form, Input, Button, message } from 'antd';
import { connect } from 'mirrorx';
import { updatePSW, loginByUserCode } from '../../../api/user';
import '../index.css';

const ChangePassword = (props) => {
  const { userCode, password } = props;

  const onFinish = params => {
    const { userCode, oldPassword, newPassword } = params;
    updatePSW({
      userCode, 
      oldPassword, 
      newPassword
    }).then(res => {
      console.log(res, 'res');
      if(res.data.code === 400) {
        return message.warn(res.data.msg);
      };
      loginByUserCode({
        userCode,
        password: newPassword,
      }).then(value => {
        localStorage.setItem('token', value.data);
        localStorage.setItem('password', newPassword);
        localStorage.setItem('userCode', userCode);
        message.success('修改成功, 即将跳转页面', 2);
        setTimeout(() => {
          props.history.push("/dashboard");
        }, 2 * 1000)
      })
    })
  }

  return (
    <Form
      className="login-form"
      initialValues={{
        userCode,
        oldPassword: password,
        newPassword: '',
        confirm: '',
      }}
      onFinish={(values) => onFinish(values)}
    >
      <h2 className="changeText">
        修改密码
      </h2>
      <p className="desc-paragraph">
        为了保证您用户信息的安全，第一次登陆时需要更改初始密码
      </p>
      <Form.Item
        name="userCode"
        rules={[{ required: true, message: '请输入用户名/学号' }]}
        style={{margin: 0,}}
      >
        <Input 
          addonBefore="学号"
          placeholder="请输入学号"
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="oldPassword"
        rules={[{ required: true, message: '请输入密码' }]}
        style={{margin: '12px 0 0 0'}}
        >
        <Input.Password
          placeholder="旧密码"
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="newPassword"
        rules={[{ required: true, message: '请输入密码' }]}
        style={{margin: '12px 0'}}
        >
        <Input.Password
          placeholder="新密码"
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="confirm"
        rules={[
          { required: true, message: '请输入密码' }, 
          ({ getFieldValue }) => ({
            validator(rule, value) {
              if (!value || getFieldValue('newPassword') === value) {
                return Promise.resolve();
              }
              return Promise.reject('两次输入密码不一致，请重新输入');
            },
          }),
        ]}
        
        >
        <Input.Password
          placeholder="再次输入新密码"
          size="large"
        />
      </Form.Item>
      <Button type="primary" size="large" htmlType="submit">确定</Button>
    </Form>
  )
}

export default connect(state => state.userInfo)(ChangePassword)