import React, { Component } from 'react';
import ModuleItem from '../ModuleItem/ModuleItem';
import Icon from '../../../public/Icon/Icon';
import ModuleDetailModal from '../ModuleDetailModal/ModuleDetailModal';

import './index.css';

export default class ColumnItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      visible: false,
      dataList: props.children,
      parentTitle: props.title,
      courseTitle: '交互设计',
    };
  }

  closeItem = () => {
    this.setState({
      visible: false,
    })
  }

  addItem = (values) => {
    const { dataList } = this.state;
    const { rangeTime } = values;
    dataList.push({
      ...values,
      createTime: values,
      creator: '高黎明',
      status: 0,
      createTime: rangeTime[0],
      finallyDate: rangeTime[1],
      level: 1,
    });

    this.setState({
      dataList,
      visible: false,
    });
  }

  render() {
    const { courseTitle, parentTitle, dataList, visible } = this.state;
    return (
      <div className="task-columnItem">
        <h3>{`${parentTitle} · ${dataList.length}`}</h3>
        {
          dataList.map((item, index) => <ModuleItem key={index} {...item} courseTitle={courseTitle} parentTitle={parentTitle} />)
        }
        <div 
          className="addBtn curser-pointer" 
          onClick={() => {
            this.setState({visible: true});
          }}>
          <Icon type="iconzu331" className="left-icon add-icon" />添加任务
        </div>
        <ModuleDetailModal 
          onCancel={() => this.closeItem()}
          onChange={values => this.addItem(values)}
          visible={visible} 
          status={0} 
          courseTitle={courseTitle} 
          parentTitle={parentTitle} 
          />
      </div>
    )
  }
}