import React, { Component } from 'react';
import { Button } from 'antd';
import { RightOutlined } from '@ant-design/icons';
import HasNoData from '../../../../public/HasNoData';
import Icon from '../../../../public/Icon/Icon';
import AddEntryModal from '../../../../public/AddEntryModal';
import TaskEditModal from '../../../../public/TaskEditorModal';
import SelectGroupModal from '../../../../public/SelectGroupModal';
import './index.css';

const statusNames = {
  'wait': '未开始',
  'doing': '正在做',
  'finish': '已完成',
}

export default class CourseDetail extends Component {
  constructor(props) {
    super(props);
    const { id } = props.match.params;

    localStorage.setItem('knowledgeId', id);
    const courseList = [];
    // mock数据
    const titleArr = ['材料工艺创新设计', '创新设计', '交互设计', '专业认知实习', '工业设计概论', '三维造型基础'];
    const attrArr = ['专业方向课', '实习', '专业理论课', '专业基础课'];
    for (let i = 0; i < 6; i++) {
      const number = parseInt(Math.random() * 4);
      courseList.push({
        id: `2y7uy3u4${i}`,
        courseIcon: 'Icon',
        courseName: titleArr[i],
        courseAttr: attrArr[number],
        courseDesc: '根据需求对车辆的造型进行推敲创新',
        courseTeacher: ['黄晶', '王海涛']
      });
    }
    this.state = {
      detailId: id,
      courseList,
      detailItem: {},
      current: 0,
      processList: [],
      hasNoData: true,
      hasNoTeam: true,
      groupDetail: {},
      isShow: false,
      entries: [],
      taskBoardDetail: {
        title: '标题：例：今天的任务是',
        desc: '标题：例：今天的任务是/标题：例：今天的任务是/标题：例：今天的任务是/标题：例：今天的任务是/标题：例：今天的任务是/'
      }
    };
  }

  componentDidMount() {
    let { detailId, courseList, detailItem, processList } = this.state;
    detailItem = courseList.filter(item => item.id === detailId)[0];
    const processNames = ['理论知识', '前期调研', '草图方案', '完成建模3D渲染', '最终定稿完成展板', '完成产品方案报告', '222'];
    const statsArr = ['wait', 'doing', 'finish'];
    for (let i = 0; i < 7; i++) {
      const order = parseInt(Math.random() * 3);
      processList.push({
        id: `skadjahsjdh${i}`,
        processName: processNames[i],
        processDesc: `${processNames[i]}___主要是用来做。。。`,
        startTime: '4月1号',
        endTime: '4月7号',
        status: statsArr[order],
      })
    }
    this.setState({
      detailItem,
      processList,
    });

  }

  onSureEntry = ({text, href}) => {
    console.log(text, href);
    const { entries } = this.state;
    entries.push({
      text,
      href,
    });

    this.setState({
      entries,
    });
  }

  onEditSure = ({title, desc}) => {
    this.setState({
      taskBoardDetail: {
        title,
        desc,
      }
    });
  }

  onSureSelect = (value) => {
    console.log(value, 'value')
  }

  render() {
    const { detailItem, processList, hasNoData, hasNoTeam, groupDetail, entries, taskBoardDetail } = this.state;
    return <div>
      <div className="detail-title-bar">
        <div className="detail-icon">
          课
        </div>
        <div className="detail-text">
          <h3>{detailItem.courseName}</h3>
          <p>{detailItem.courseDesc}</p>
        </div>
      </div>
      <div className="detail-content">
        <div className="left-area">
          <div className="keyboard">
            <h3>公告板</h3>
            <div className="keyboard-content">
              <img src={require('../../../../assets/images/teacher0.png')} alt=""/>
              <div className="desc">
                <div className="courseTitle">
                  <h2>{taskBoardDetail.title}</h2>
                  <Button 
                    style={{background: 'rgba(58, 146, 249, .2)',borderRadius: '4px', border: 0, color:'#3A92F9'}}
                    onClick={() => TaskEditModal.show({
                      onSure: this.onEditSure
                    })}
                  >
                    编辑
                  </Button>
                </div>
                <p className="courseDesc">
                  {taskBoardDetail.desc}
                </p>
                <br />
              </div>
            </div>
          </div>
          <div className="taskBoard">
            <div className="title">
              <h3>任务看板</h3>
              <a href="#/taskTable" className="taskDetailBtn">查看任务详情<RightOutlined /></a>
            </div>
            <div className="taskBoard-content">
              <div className="process-map" style={{width: `${processList.length * 310 + 50 }px`, display: processList.length < 4 ? 'flex' : 'block'}}>
                {
                  processList.map(item => (
                    <div key={item.id} className={`process-item ${item.status}-item`}>
                      <div className="process-title">
                        <h3>{item.processName}</h3>
                        <p>{statusNames[item.status]}</p>
                      </div>
                      <p className="desc">
                        {item.processDesc}
                      </p>
                      <p className="time">
                        {`${item.startTime}-${item.endTime}`}
                      </p>
                    </div>
                  ))
                }
              </div>
            </div>
          </div>
          <div className="newLatestBoard">
            <h3>最新发布</h3>
            {hasNoData && <HasNoData />}
          </div>
        </div>
        <div className="right-area">
          <div className="knowledge-module">
            <h3>课程知识库</h3>
            <div className="content">
              <div className="titleBar">
                <div className="title">
                  <div className="icon">

                  </div>
                  <dl>
                    <dt>产品手册</dt>
                    <dd>产品说明以及功能使用</dd>
                  </dl>
                </div>
                <p className="orderList"></p>
              </div>
              <ul className="dialogList">
                <li>
                  <span>产品历史版本</span>
                  <span>今天 15:30</span>
                </li>
                <li>
                  <span>产品简介</span>
                  <span>今天 16:30</span>
                </li>
                <li>
                  <span>产品功能以及使用</span>
                  <span>今天 17:30</span>
                </li>
              </ul>
            </div>
          </div>
          <div className="team-module">
            <h3>我的课程团队</h3>
            {
              hasNoTeam ? <div className="createNew">
                <Button 
                  onClick={() => {
                    this.props.history.push('/group/createNew')
                  }}
                  style={{color: '#3A92F9', height: '40px', borderRadius: '4px'}}>建立团队</Button>
                <Button 
                  onClick={
                    () => SelectGroupModal.show({
                      onSelect: this.onSureSelect
                    })
                  } 
                  style={{color: '#3A92F9', height: '40px', borderRadius: '4px'}}
                >
                  加入团队
                </Button>
              </div> : <div className="group-desc">
                <span>
                  {!groupDetail.avatar && <Icon className="group-avatar-icon" type="iconzu22" />}
                </span>
                <dl 
                  className="group-info curser-pointer"
                  onClick={() => this.props.history.push(`/group/detail/${groupDetail.id}`)}
                >
                  <dt>{groupDetail.name}</dt>
                  <dd className="desc-paragraph">{groupDetail.desc}</dd>
                </dl>
              </div>
            }
          </div>
          <div className="entries-module">
            <h3>快捷入口</h3>
            <ul className="entries">
              {
                entries.map(item => {
                  const url = !item.href.includes('http') ? `http://${item.href}` : item.href;
                  return <li>
                  <a className="link" target="blank" href={url}>
                    {item.text}
                  </a>
                </li>
                })
              }
              <li>
                <a
                  href="javascript:;"
                  onClick={() => AddEntryModal.show({
                    onSure: this.onSureEntry
                  })
                }>
                  添加
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  }
}
