import React, { useState } from 'react';
import { connect } from 'mirrorx';
import { Form, Button, Input } from 'antd';
import { getValidateCodeData, bindPhone } from '../../../api/user';

const BindPhone = (props) => {
  const [seconds, setSeconds] = useState(60);
  const [isTimeout, setIsTimeout] = useState(false);
  const [isStartTimeout, setIsStartTimeout] = useState(false);
  const [canSendCode, setCanSendCode] = useState(false);
  const [phone, setPhone] = useState('');
  const [validateCode, setValidateCode] = useState('');
  const onFinish = (values) => {
    const { userCode } = props;
    const { validateCode, phone } = values;
    
    // 绑定手机号接口
    // bindPhone({code: validateCode, phone, userCode}).then(res => {
    //   console.log(res, 'res', '登陆成功');
    // });

    window.location.href="#/dashboard"
  }

  const getValidateCode = () => {
    // 判断是否填写了电话号码
    if (!canSendCode) return ;

    // 初始化；
    setIsStartTimeout(true);
    setSeconds(60);
    let count = 60;

    // 计时器
    const timer = setInterval(() => {
      count--;
      setSeconds(count);
      if (count <= 0) {
        clearInterval(timer);
        setSeconds(60);
        setIsStartTimeout(false);
        setIsTimeout(true);
      };
    }, 1000);

    // 发起请求，获取验证码
    getValidateCodeData({phone}).then(res => {
      if (res.data.code === 1) return;
      setValidateCode(res.data);
      clearInterval(timer);
    })
    
  }

  const renderValidateBtn = () => {
    return (
      <span onClick={() => getValidateCode()} className={canSendCode ? 'link' : 'disabled'}>
        {isTimeout ? '重新' : ''}
        获取验证码
      </span>
    )
  }

  const renderTimeout = () => {
    return (
      <span>
        <span className="link">
          {seconds}
        </span>
        秒之后重试
      </span>
    )
  }

  return (
    <Form
      className="login-form"
      style={{height: '360px'}}
      onFinish={(values) => onFinish(values)}
    >
      <img className="login-logo" src={require('../../../assets/images/logo.png')} alt=""/>
      <h1 className="logo-title">鲸落</h1>
      <p className="logo-desc">后期可通过手机号找回密码</p>
      <Form.Item
        name="phone"
        rules={[
          { required: true, message: '请输入手机号' },
          ({ getFieldValue }) => ({
            validator(rule, value) {
              const myReg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
              if(!value || myReg.test(value)) {
                return Promise.resolve();
              }
              if (value && myReg.test(value)) {
                setCanSendCode(true);
                setPhone(value);
                return Promise.resolve();
              }
              return Promise.reject('手机号格式不正确');
            },
          }),
        ]}
      >
        <Input 
          addonBefore="手机号"
          placeholder="请输入手机号"
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="validateCode"
        rules={[{ required: true, message: '验证码不能为空' }]}
      >
        <Input
          placeholder="请输入验证码"
          size="large"
          addonAfter={isStartTimeout ? renderTimeout() : renderValidateBtn()}
          value={validateCode}
          onChange={(e) => setValidateCode(e.target.value)}
        />
      </Form.Item>
      <Button 
        type="primary" 
        size="large" 
        htmlType="submit"
      >确定</Button>
    </Form>
  )
}

export default connect(state => state.userInfo)(BindPhone)