import React, { Component } from 'react';
import { Menu, Dropdown, Tooltip, Skeleton, Tree, message } from 'antd';
import TreeListMenu from '../../../../public/TreeList/TreeLIstMenu';
import Icon from '../../../../public/Icon/Icon';
import { getKnowledgeDetail } from '../../../../api';
import moment from 'moment';

const icons = ['iconlujing243', 'iconlujing46']
export default class KnowledgeDetail extends Component {
  constructor(props) {
    super(props);

    window.localStorage.setItem('knowledge', props.match.params.id);

    this.state = {
      baseId: props.match.params.id - 0,
      belongs: '',
      sharedSight: 0,
      isCollect: false,
      openMenu: false,
      openManagerMenu: false,
      name: '前期调研分析',
      desc: '为了定位产品，必须要认真分析市场和竞品的优势',
      operaters: [
        {
          name: '王傲飞',
          avatar: undefined,
          studentCode: '311617040114',
        },
        {
          name: '丁义秋',
          avatar: undefined,
          studentCode: '311617040120',
        },
        {
          name: '赵柯',
          avatar: undefined,
          studentCode: '311617040121',
        },
        {
          name: '高黎明',
          avatar: undefined,
          studentCode: '311617040124',
        },
      ],
      menus: [],
      oneMenu: [],
      secondMenu: [],
      docs: [],
      loading: true,
    };
  }

  async componentDidMount () {
    const { baseId } = this.state;
    const ret = await getKnowledgeDetail({ baseId });
    console.log(ret)
    if(!ret) return message.error('获取知识库信息失败，请尝试重新登陆账号');
    const oneMenu = [];
    const secondMenu = [];
    const docs = [];
    const createTime = moment().format("LT");
    
    for (let i = 0; i < 9; i++) {
      oneMenu.push({
        title: `一级目录${i}`,
        createTime,
        id: `${i}`,
        isEmpty: i % 2 === 0,
      });
      
      secondMenu.push({
        parentId: `${i}`,
        title: `二级目录${i}`,
        createTime,
        id: `${i}${i}`,
        isEmpty: i % 2 !== 0,
      });
      
      docs.push({
        parentId: i % 2 !== 0 ? `${i}` : `${i}${i}`,
        title: `知识库文档_${i}`,
        id: `${i}${i}${i}`,
        createTime,
        isEmpty: true,
      });
    };
    
    const menus = oneMenu.map(menu => {
      const children = [];
      const targetSecondMenu = secondMenu.filter(item => item.parentId === menu.id);
      targetSecondMenu.map(item => {
        const secondChildren = [];
        const targetDocs = docs.filter(doc => doc.parentId === item.id);
        targetDocs.map(doc => {
          secondChildren.push({
            ...doc,
            key: doc.id,
          })
        });
        
        children.push({
          ...item,
          key: item.id,
          children: secondChildren,
          isLeaf: false,
        });
      });
      const oneDocs = docs.filter(doc => doc.parentId === menu.id);
      oneDocs.map(doc => {
        children.push({
          ...doc,
          key: doc.id,
        })
      });
      
      return {
        ...menu,
        key: menu.id,
        children,
      }
    });

    this.setState({
      loading: false,
    });

    this.setState({
      belongs: ret.data.parentName,
      desc: ret.data.baseInfo,
      sharedSight: ret.data.isPublic - 0,
      menus,
      oneMenu,
      secondMenu,
      docs,
      name: ret.data.baseName,
    })
  }
  
  renderTreeNode = ({title, createTime}) => {
    return (
      <div className="flex">
        <span className="title">
          {title}
        </span>
        <span className="line">
        </span>
        <span className="affixInfo">
          {createTime}
        </span>
      </div>
    )
  }


  renderMenu = () => {
    return (
      <Menu>
        <Menu.Item>
          <a>新建文档</a>
        </Menu.Item>
        <Menu.Item>
          <a>新建画板</a>
        </Menu.Item>
        <Menu.Item>
          <a>从模板新建</a>
        </Menu.Item>
        <Menu.Item>
          <a>导入</a>
        </Menu.Item>
      </Menu>
    )
  }

  renderManagerMenu = () => {
    const { id } = this.props.match.params; 
    return (
      <Menu>
        <Menu.Item>
          <a target="_blank" href={`#/knowledge/manager/member/${id}`}>知识库成员</a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" href={`/knowledge/manager/analysis/${id}`}>统计</a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" href={`#/knowledge/manager/recycle/${id}`}>回收站</a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" href={`#/knowledge/manager/setting/${id}`}>设置</a>
        </Menu.Item>
      </Menu>
    )
  }

  onItemClick = (item) => {
    const { isEmpty, key } = item;
    if (!isEmpty) {
      this.props.history.push(`/docDetail/${key}`);
    }
  }

  render() {
    const { 
      sharedSight, 
      isCollect, 
      openMenu, 
      openManagerMenu, 
      name, 
      desc,
      operaters,
      menus,
      belongs,
      loading,
    } = this.state;
    return <div className="knowledge-detail">
      <div className="title-bar-public flex">
        <div className="left-area">
          <div className="avatar">
            <img src={require('../../../../assets/images/logo.png')} alt=""/>
          </div>
          <div className="title-area">
            <div className="title">
              <h3>{belongs} <Icon type={icons[sharedSight]}/></h3>
              <span 
                onClick={() => {
                  const { isCollect } = this.state;
                  this.setState({
                    isCollect: !isCollect,
                  });
                }} 
                style={{color: isCollect && '#999', borderColor: isCollect && '#999'}}
                className={`collectBtn curser-pointer`}
              >
                {
                  isCollect ? '已收藏' : '收藏 +'
                }
              </span>
            </div>
            <p className="desc-paragraph">
              打造专业知识库，增强学生与教师的信息交流，位专业学生打造更为专业的记录知识的平台
            </p>
          </div>
        </div>
        <div className="right-area flex">
          <span className="primaryBtn">分享</span>
          <div className="create-doc flex">
            <div className="actionBtn curser-pointer">
              新建文档
            </div>
            <Dropdown
              className="dropdown"
              overlay={
                () => this.renderMenu()
              }
              overlayClassName="functionMenu"
              trigger="hover"
              visible={openMenu}
              onVisibleChange={
                visible => {
                  this.setState({
                    openMenu: visible,
                  });
                }
              }
              placement="bottomRight"
            >
              <span>
                <Icon type={!openMenu ? 'iconlujing100' : 'iconlujing247'} /> 
              </span>
            </Dropdown>
          </div>
          <Dropdown
            overlay={
              () => this.renderManagerMenu()
            }
            overlayClassName="functionMenu"
            visible={openManagerMenu}
            onVisibleChange={
              visible => {
                this.setState({
                  openManagerMenu: visible,
                });
              }
            }
            placement="bottomRight"
          >
            <span>
              <Icon className="bigIcon" type="iconzu334"/>
            </span>
          </Dropdown>
        </div>
      </div>
      <div className="main-content border">
        <span className="editMenuBtn curser-pointer">编排目录</span>
        <div className="title">
          <h2>{name}</h2>
          <p className="desc-paragraph">{desc}</p>
        </div>
        <div className="operaters">
          {
            operaters.map(operater => (
              <Tooltip className="operateItem">
                {
                  operater.avatar ? 
                    <img src={operater.avatar} /> : 
                    <img src={require('../../../../assets/images/default_avatar.png')} />
                }
              </Tooltip>
            ))
          }
        </div>
        <Skeleton loading={loading} paragraph={{ rows: 6 }} active>
          <TreeListMenu gData={menus} onItemClick={this.onItemClick} />
        </Skeleton>
      </div>
    </div>
  }
}