import React, { Component } from 'react';
import { Radio, Button, Form, Input, Select, Avatar, Upload, message } from 'antd';
import Icon from '../../../../public/Icon/Icon';
import { createNewGroup } from '../../../../api/group';
import { actions } from 'mirrorx';
import UploadAvatar from '../../../../public/Upload';

import './index.css';

const { Option } = Select;

export default class GroupCreateNew extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentStep: 0,
      templateType: 'Auto',
      templateOptionsPerson: [],
      templateOptionsClass: [],
      currentTag: 'person',
      selectedTemplate: {},
      userInfos: {},
      selectedTemplateInfo: {},
      imgUrl: undefined,
    };
  }

  async componentDidMount() {
    let { templateOptionsPerson, templateOptionsClass, currentTag } = this.state;

    const userInfos = await actions.userInfo.getUserInfos();
    // mock数据
    const templateNames = '考研小组_建模小组_调研小组_英语小组_编程小组_手绘小组'.split('_');
    const templateDesc = '共享考研资料和历年考研真题和考研技巧_交流分享建模心得，提供3D模型在线渲染能力_专门从事用户研究调查，共享调研资料_英语四，六级不再是遥不可及的梦_内含新手入门教程以及编码规范_手绘爱好小组，画出自己喜欢的事物'.split('_')
    templateOptionsPerson = templateNames.map((item, index) => ({
      id: `template_person_${index}`,
      templateName: item,
      templateDesc: templateDesc[index],
    }));

    const templateClassNames = '交互设计_智能家居系统设计_装备设计_汽车造型设计_材料工艺创新设计'.split('_');
    const templateClassDesc = '探讨交互的本质和如何做交互_思考智能家居系统如何设计_从造型，功能，考量_挖掘学生对于造型的美感_研究新材料新工艺带来的创新价值'.split('_')    

    templateOptionsClass = templateClassNames.map((item, index) => ({
      id: `template_class_${index}`,
      templateName: item,
      templateDesc: templateClassDesc[index],
    }));

    this.setState({
      templateOptionsPerson,
      templateOptionsClass,
      selectedTemplate: 
        currentTag === 'class' ? 
        templateOptionsClass[0].id : 
        templateOptionsPerson[0].id,
      userInfos,
      selectedTemplateInfo: currentTag === 'class' ? 
        templateOptionsClass[0] : 
        templateOptionsPerson[0],
    });
  }

  onCreate = (values) => {
    const { selectedTemplateInfo, currentTag, userInfos, imgUrl } = this.state;
    const { groupName, groupDesc } = values;
    const belong = currentTag === 'class' ? selectedTemplateInfo.templateName : userInfos.name;
    createNewGroup({
      createFrom: currentTag === 'person' ? 1 : 0,
      groupInfo: groupDesc,
      groupName,
      isPublic: '0',
    }).then(res => {
      if (res.code === 200) {
        message.success('新建成功，即将跳转页面');
        // ToDoList 后端返回新增知识库id，换成这个字段
        window.location.href = `#/group/detail/${res.data}`
      }
    })
  }

  onStepChange = (step) => {
    this.setState({
      currentStep: step,
    });
  }


  onSelectTag = (e) => {
    const { templateOptionsClass, templateOptionsPerson } = this.state;

    this.setState({
      currentTag: e.target.value,
      selectedTemplate: e.target.value === 'class' ? 
        templateOptionsClass[0].id : 
        templateOptionsPerson[0].id,
        selectedTemplateInfo: e.target.value === 'class' ? 
        templateOptionsClass[0] : 
        templateOptionsPerson[0],
    });
  }

  onSelectTemplate = e => {
    const { templateOptionsClass, templateOptionsPerson } = this.state;
    const arr = [...templateOptionsClass, ...templateOptionsPerson];
    this.setState({
      selectedTemplate: e.target.value,
      selectedTemplateInfo: arr.find(item => item.id === e.target.value),
    })
  }

  onUploadImg = (imgUrl) => {
    this.setState({
      imgUrl,
    })
  }

  renderLeftSection = () => {
    const { 
      currentStep, 
      templateOptionsPerson, 
      templateOptionsClass, 
      currentTag, 
      selectedTemplate,
      userInfos,
      selectedTemplateInfo,
      imgUrl
    } = this.state;
    const options = currentTag === 'class' ? templateOptionsClass : templateOptionsPerson;
    const belong = currentTag === 'class' ? selectedTemplateInfo.templateName : userInfos.name;
    
    if (currentStep - 0 === 0) {
      return (
        <>
          <div className="tagSection section">
            <h3>标签</h3>
            <Radio.Group onChange={this.onSelectTag} className="tagSelector" value={currentTag} buttonStyle="solid">
              <Radio.Button value="person">
                个人
              </Radio.Button>
              <Radio.Button value="class">
                课程
              </Radio.Button>
            </Radio.Group>
          </div>
          <div className="templateSection section">
            <h3>{currentTag === 'class' ? '从课程新建' : '从模板新建' }</h3>
            <Radio.Group onChange={this.onSelectTemplate} className="templateSelector" buttonStyle="solid" value={selectedTemplate}>
              {
                options.map(template => (
                  <Radio.Button value={template.id}>
                    <h3>
                      {template.templateName}
                    </h3>
                    <p className="desc-paragraph">
                      {template.templateDesc}
                    </p>
                  </Radio.Button>
                ))
              }
            </Radio.Group>
          </div>
          <Button type="primary" onClick={() => this.onStepChange(1)} className="actionButton">
            下一步
          </Button>
        </>
      )
    } else if (currentStep - 0 === 1) {
      return (
        <>
          <div className="goBack">
            <Icon onClick={() => this.onStepChange(0)} type="iconlujing36" />
            <span className="tipText">返回重新选择</span>
          </div>
          <Form
            className="groupForm"
            layout="vertical"
            onFinish={values => this.onCreate(values)}
          >
            <Form.Item
              label="名称"
              name="groupName"
              rules={[{ required: true, message: '请输入团队名称' }]}
            >
              <Input placeholder={selectedTemplateInfo.templateName} />
            </Form.Item>
            <Form.Item
              label="简介"
              name="groupDesc"
            >
              <Input.TextArea placeholder={selectedTemplateInfo.templateDesc} />
            </Form.Item>
            <Form.Item
              label="归属"
              name="belongs"
              value={belong}
            >
              <div className="belongTag">
                {`${belong}（${currentTag === 'person' ? '个人' : '课程'}）`}
              </div>
            </Form.Item>
            <Form.Item
              label="可见范围"
              name="visibleRange"
            >
              <Select className="visibleRangeSelector" defaultValue="team">
                <Option value="team">
                  <h3>
                    <Icon className="marginRight" type="iconlujing40" />仅团队成员可见
                  </h3>
                  <p className="desc-paragraph">
                    知识库仅团队成员可见
                  </p>
                </Option>
                <Option value="public">
                  <h3>
                    <Icon className="marginRight" type="iconlujing40" />
                    互联网可见
                  </h3>
                  <p className="desc-paragraph">
                    向社区公开团队知识库
                  </p>
                </Option>
              </Select>
            </Form.Item>
            <Form.Item
              label="头像"
              name="avatar"
            >
              <div className="flex noJustify">
                <Avatar style={{marginRight: 24}} size={100} src={imgUrl} />
                <UploadAvatar onUpload={this.onUploadImg} />
              </div>
            </Form.Item>
            <Form.Item>
              <Button htmlType="submit" className="primaryBtn" block={true} type="primary">创建</Button>
            </Form.Item>
          </Form>
        </>
      )
    }
  }

  renderRightSection = () => {
    const { templateType } = this.state;
    
    switch (templateType) {
      case 'Auto':
        return (
          <>
            <div className="templateTitle">
              <h3 className="noMargin">画板知识库</h3>
              <p className="desc-paragraph">
                提供结构化展示，在线完成知识展示。
              </p>
            </div>
            <div className="templateSlotPic">
              {/* 团队插画 */}
              <img src={require('../../../../assets/images/placeHolder.jpg')} />
            </div>
            <div className="useScene">
              <p className="noMargin">使用场景</p>
              <p className="desc-paragraph">使用场景使用场景使用场景使用场景使用场景使用场景</p>
            </div>
          </>
        )
        break;
    
      default:
        break;
    }
  }

  render() {
    return (
      <div className="createNewWrap">
        <div className="title flex noJustify">
          <h3>新建团队</h3>
          <span>一起收集资料，交流灵感，沉淀经验</span>
        </div>
        <div className="createContent flex">
          <div className="leftSection padding flex">
            {this.renderLeftSection()}
          </div>
          <div className="rightSection padding">
            {this.renderRightSection()}
          </div>
        </div>
      </div>
    )
  }
}