import React from 'react';
import { Router, Route, Switch, Redirect } from 'react-router-dom';
import { createHashHistory } from 'history';
import Main from '../../components/DashboardMainContent/Main';
import Course from '../../components/DashboardMainContent/Course';
import Group from '../../components/DashboardMainContent/Group';
import Knowledges from '../../components/DashboardMainContent/Knowledges';
import FilesList from '../../components/DashboardMainContent/FilesList';
import Concerns from '../../components/DashboardMainContent/Concerns';
import Collections from '../../components/DashboardMainContent/Collections';
import RecycleBin from '../../components/DashboardMainContent/RecycleBin';
import LatestViews from '../../components/DashboardMainContent/LatestViews';
import Products from '../../components/DashboardMainContent/Products';
const history = createHashHistory();

export default function Dashboard(props) {
  return (
    <div>
      <Router history={history}>
        <Switch>
          <Route path="/dashboard/main" exact component={(props) => <Main {...props} />} />
          <Route path="/dashboard/course" exact component={() => <Course />} />
          <Route path="/dashboard/group" exact component={(props) => <Group {...props} />} />
          <Route path="/dashboard/knowledges" component={(props) => <Knowledges {...props} />} />
          <Route path="/dashboard/docs" component={() => <FilesList />} />
          <Route path="/dashboard/products" component={() => <Products />} />
          <Route path="/dashboard/concerns" component={() => <Concerns />} />
          <Route path="/dashboard/collections" component={() => <Collections />} />
          <Route path="/dashboard/latestViews" component={() => <LatestViews />} />
          <Route path="/dashboard/recycleBin" component={() => <RecycleBin />} />
        </Switch>
      </Router>
    </div>
  )
}