import React, { Component } from 'react';
import Icon from '../../../../public/Icon/Icon';
import { NavLink, Router, Switch, Route } from 'react-router-dom';
import KnowledgeMember from '../KnowledgeMember/KnowledgeMember';
import KnowledgeAnalysis from '../KnowledgeAnalysis/KnowledgeAnalysis';
import KnowledgeRecycle from '../KnowledgeRecycle/KnowledgeRecycle';
import KnowledgeSetting from '../KnowledgeSetting/KnowledgeSetting';
import { createHashHistory } from 'history';
import { getKnowledgeDetail } from '../../../../api';
import './index.css';

const history = createHashHistory();

export default class KnowledgeManager extends Component {
  constructor(props) {
    super(props);
    const { pathname } = props.location;
    const { type, id } = props.match.params;
    if (pathname === '/knowledge/manager') {
      props.history.replace('/knowledge/manager/member')
    }

    this.state = {
      knowledgeDetail: {},
      url: '/knowledge/manager',
      tab: type,
      id,
    }
  }

  componentDidMount = async() => {
    const { data } = await getKnowledgeDetail({
      baseId: this.state.id,
    });

    this.setState({
      knowledgeDetail: data,
    })
  }

  render() {
    const { knowledgeDetail, url, id } = this.state;
    return (
      <div className="knowledgeManager">
        <div className="title-bar-public flex noJustify">
          <h2 onClick={() => {
            this.props.history.push(`/knowledge/detail/${id}`);
          }} className="title flex curser-pointer">
            <Icon className="defaultIcon" type="iconzu190" />
            {
              knowledgeDetail.baseName
            }
          </h2>
          <div className="nav">
            <NavLink key={`#/${url}/member/${id}`} className="group-menu-item" activeClassName="active" to={`${url}/member/${id}`}>成员</NavLink>
            <NavLink className="group-menu-item" activeClassName="active" to={`${url}/analysis/${id}`}>统计</NavLink>
            <NavLink className="group-menu-item" activeClassName="active" to={`${url}/recycle/${id}`}>回收站</NavLink>
            <NavLink className="group-menu-item" activeClassName="active" to={`${url}/setting/${id}`}>设置</NavLink>
          </div>
        </div>
        <div className="main-content">
          <Router history={history}>
            <Switch>
              <Route exact path={`${url}/member/${id}`} component={() => <KnowledgeMember knowledgeDetail={knowledgeDetail} />} />
              <Route exact path={`${url}/analysis/${id}`} component={() => <KnowledgeAnalysis knowledgeDetail={knowledgeDetail} />} />
              <Route exact path={`${url}/recycle/${id}`} component={() => <KnowledgeRecycle knowledgeDetail={knowledgeDetail} />} />
              <Route exact path={`${url}/setting/${id}`} component={() => <KnowledgeSetting knowledgeDetail={knowledgeDetail} />} />
            </Switch>
          </Router>
        </div>
      </div>
    )
  }
}