import React, { Component } from 'react';
import { createFromIconfontCN } from '@ant-design/icons';
import { NavLink, Router, Switch, Route } from 'react-router-dom';
import { createHashHistory } from 'history';
import GroupMainContent from '../GroupMainContent';
import Knowledges from '../../Knowledges';
import GroupMember from '../GroupMember/GroupMember';
import GroupRecycle from '../GroupRecycle/GroupRecycle';
import GroupSetting from '../GroupSetting/GroupSetting';
import GroupDataAnalysis from '../GroupDataAnalysis/GroupDataAnalysis';
import { getGroupDetail } from '../../../../api/group';
import Icon from '../../../../public/Icon/Icon';

import './GroupDetail.css';

const history = createHashHistory();

const icons = ['iconlujing243', 'iconlujing46']

export default class GroupDetail extends Component {
  constructor(props) {
    super(props);

    const knowledgeColumns = [
      {
        title: '名称',
        key: 'baseName',
        width: 280,
      },
      {
        title: '简介',
        key: 'baseInfo',
        width: 400,
        className: 'desc-paragraph',
      },
      {
        title: '最后更新时间',
        key: 'latestTime',
        width: 320,
        render: (rowItem, index) => {
          return <p className="ellipsis" style={{lineHeight: '26px', margin: 0, paddingRight: '12px'}}>
            <strong>{rowItem.behavior}</strong>
            <span style={{margin: '0 10px'}}>更新于</span>
            <strong>今天 {rowItem.updatedTime}</strong>
          </p>
        }
      }
    ]

    const { id } = props.match.params;
    const isRedirect = props.location.pathname.split(id)[1] == '';
    if (isRedirect) {
      
      props.history.replace(`${props.location.pathname}/main`)
    };

    this.state = {
      groupId: parseInt(id),
      knowledgeColumns,
      groupDetail: {},
    }
  }

  componentDidMount() {
    this.fetchData();
  }
  
  fetchData = async() => {
    const { groupId } = this.state;
    const ret = await getGroupDetail({groupId});
    this.setState({
      groupDetail: ret.data,
    });
    
  }

  render() {
    const { knowledgeColumns, groupId, groupDetail } = this.state;
    const { url } = this.props.match;
    return (
      <div className="group-detail">
        <div className="title-bar-public group-special">
          <div className="avatar">
            {groupDetail.headImg ? <img src={groupDetail.headImg} alt=""/> : 'T'}
          </div>
          <div className="title-area">
            <div className="title">
              <h3>{groupDetail.groupName} <Icon type={icons[groupDetail.isPublic - 0]}/></h3>
              <a href="" className="collectBtn">收藏 +</a>
            </div>
            <p className="desc-paragraph">
              {groupDetail.groupInfo}
            </p>
            <div className="nav flex">
              <NavLink className="group-menu-item" activeClassName="active" to={`main`}>首页</NavLink>
              <NavLink className="group-menu-item" activeClassName="active" to={`books`}>知识库</NavLink>
              <NavLink className="group-menu-item" activeClassName="active" to={`member`}>成员</NavLink>
              <NavLink className="group-menu-item" activeClassName="active" to={`analysis`}>统计</NavLink>
              <NavLink className="group-menu-item" activeClassName="active" to={`recycle`}>回收站</NavLink>
              <NavLink className="group-menu-item" activeClassName="active" to={`setting`}>设置</NavLink>
            </div>
          </div>
        </div>
        <div className="main-content">
          <Router history={history}>
            <Switch>
              <Route exact path={`${url}/main`} component={(props) => <GroupMainContent memberList={groupDetail.student} history={props.history} fetchPage={this.fetchData} groupId={groupId} />} />
              <Route exact path={`${url}/books`} component={() => <Knowledges groupId={groupId} columns={knowledgeColumns} />} />
              <Route exact path={`${url}/member`} component={() => <GroupMember memberList={groupDetail.student || []} />} />
              <Route exact path={`${url}/analysis`} component={() => <GroupDataAnalysis />} />
              <Route exact path={`${url}/recycle`} component={() => <GroupRecycle />} />
              <Route exact path={`${url}/setting`} component={() => <GroupSetting />} />
            </Switch>
          </Router>
        </div>
      </div>
    )
  }
}