import React, { Component } from 'react';
import SearchBar from '../../../../public/SearchBar';
import moment from 'moment';
import SharedTable from '../../../../public/SharedTable';
import DialogModal from '../../../../public/DialogModal/DialogModal';
import { message } from 'antd';

export default class KnowledgeRecycle extends Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: '名称',
        key: 'docName',
        width: 210,
      }, 
      { 
        title: '操作者',
        key: 'behavior',
        width: 210,
      },
      // {
      //   title: '归属',
      //   key: 'belong',
      //   width: 240,
      // },
      {
        title: '删除时间',
        key: 'deleteTime',
        width: 100,
      },
      {
        title: '',
        width: 150,
        render: (item, index) => {
          return <div className="action-btns">
            <a href="javascript:;" onClick={() => this.reOpen(item, index)}>恢复</a>
            <a href="javascript:;" onClick={() => this.delete(item, index)}>彻底删除</a>
          </div>
        }
      }
    ];

    this.state = {
      dataList: [],
      keywords: undefined,
      columns,
      checkedIds: [],
      loading: false,
    }
  }

  componentDidMount = () => {
    this.fetchData();
  }

  fetchData = () => {
    this.setState({
      loading: true,
    });

    setTimeout(() => {
      const { keywords } = this.state;
      const arr = [];
      const date = moment().format('LT');
      // mock数据
      for (let i = 0; i < 10; i++) {
        arr.push({
          docName: keywords || '默认文档标题1',
          id: `2huhjkd${i}`,
          deleteTime: date,
          behavior: '黑白之间',
        });
      };
  
      this.setState({
        dataList: arr,
        loading: false,
      });

    }, 600)
  }

  reOpen = (item, index) => {
    DialogModal.show({
      title: '恢复数据',
      content: '该操作恢复该条数据，请确认是否恢复',
      onOk: async () => {
        console.log('请求删除接口');
        await this.fetchData();
        message.success('恢复成功');
      },
      type: 'warning',
    });
    console.log('恢复', item)
  }

  delete = (item, index) => {
    DialogModal.show({
      title: '彻底删除',
      content: '该操作将永久删除该文档，请确定是否彻底删除',
      onOk: async() => {
        await this.fetchData();
        message.success('删除成功');
      },
      type: 'warning'
    });
  }

  onSearchDocs = keywords => {
    this.setState({
      keywords,
    }, () => this.fetchData());
  }

  renderExtraBtn = () => {
    return (
      <div className="batchBtn">
        <span onClick={() => this.batchReOpen()} className="link">批量恢复</span>
        <span onClick={() => this.batchDelete()} className="link">批量删除</span>
      </div>
    )
  }

  batchReOpen = () => {
    const { checkedIds } = this.state;
    DialogModal.show({
      title: '批量删除',
      content: `该操作恢复${checkedIds.length}条数据，请确认是否恢复`,
      onOk: async() => {
        console.log('请求恢复接口');
        await this.fetchData();
        message.success('恢复成功');
      },
      type: 'warning'
    });
    console.log('批量恢复文档', checkedIds);
  }

  batchDelete = () => {
    const { checkedIds } = this.state;
    DialogModal.show({
      title: '批量删除',
      content: `该操作将永久删除${checkedIds.length}个文档，请确定是否彻底删除`,
      onOk: async() => {
        console.log('请求删除接口');
        await this.fetchData();
        message.success('删除成功');
      },
      type: 'warning'
    });
    console.log('批量彻底删除文档', checkedIds);
  }

  getCheckedList = values => {
    console.log(values, 'values')
    this.setState({
      checkedIds: values,
    })
  }

  render() {
    const { columns, dataList, loading } = this.state;
    return (
      <div className="knowledgeRecycle">
        <SearchBar 
          onSearch={values => this.onSearchDocs(values)} 
          searchTitle="回收站"  
        />
        <SharedTable 
          loading={loading}
          renderExtraBtn={() => this.renderExtraBtn()} 
          hasCheckedBox 
          columns={columns} 
          dataSource={dataList} 
          getCheckedList={values => this.getCheckedList(values)}
        />
      </div>
    )
  }
}