import React, { Component } from 'react';
import SearchBar from '../../../../public/SearchBar';
import SharedTable from '../../../../public/SharedTable';
import Icon from '../../../../public/Icon/Icon';

import './index.css';

export default class GroupRecycle extends Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: '名称',
        key: 'docName',
        width: 210,
      }, 
      { 
        title: '操作者',
        key: 'behavior',
        width: 210,
      },
      {
        title: '归属',
        key: 'belong',
        width: 240,
      },
      {
        title: '删除时间',
        key: 'deleteTime',
        width: 100,
      },
      {
        title: '',
        width: 150,
        render: (item, index) => {
          return <div className="action-btns">
            <a href="" onClick={() => this.reOpen(item, index)}>恢复</a>
            <a href="" onClick={() => this.delete(item, index)}>彻底删除</a>
          </div>
        }
      }
    ];

    this.state = {
      columns,
      dataSource: [],
    }
  }

  componentDidMount() {
    const { dataSource } = this.state;

    for (let i = 0; i < 6; i++) {
      dataSource.push({
        id: `sajddhaj${i}`,
        docName: '团队周报',
        behavior: '高黎明',
        belong: '交互毕设小组',
        deleteTime: '今天 17:06',
        icon: <Icon className="middle-icon" type="iconzu112-copy" />
      })
    };

    this.setState({
      dataSource,
    })
  }

  render() {
    const { columns, dataSource } = this.state;
    return <div className="knowledge-list">
      <SearchBar searchTitle="回收站" noActions/>
      <SharedTable columns={columns} dataSource={dataSource} />
    </div>
  }
}