import React, { Component } from 'react';
import { Menu, Input, Button, Checkbox, Select, message } from 'antd';
import DialogModal from '../../../../public/DialogModal/DialogModal';
import { updateKnowledgeDetail } from '../../../../api';

const { Option } = Select;

export default class KnowledgeSetting extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedKeys: ['knowledgeSetting'],
      options: [],
      selectedGroupId: undefined,
      knowledge: {
        ...props.knowledgeDetail,
        isPublic: props.knowledgeDetail.isPublic !== '0',
      },
      edited: false,
    }
  }

  onSelect = e => {
    console.log(e);
    this.setState({
      selectedKeys: [e.key],
    })
  }

  componentDidMount() {
    this.fetchOptions();
  }

  fetchOptions = async () => {
    const options = [];

    for (let i = 0; i < 9; i++) {
      options.push({
        value: `sdjkah${i}`,
        label: `默认知识库${i}`,
      })
    };

    this.setState({
      options,
    });

  }

  onChangeGroup = () => {
    const knowledgeId = localStorage.getItem('knowledge');
    const { knowledge, selectedGroupId } = this.state;
    if (!selectedGroupId) {
      message.warn('请先选择目标团队');
      return;
    }
    DialogModal.show({
      title: '转移知识库',
      content: `正在删除知识库 ${knowledge.name}，该操作不可逆，一旦操作成功，知识库下的所有内容将会删除。请输入下面内容再次确认操作。`,
      onOk: () => {
        console.log('请求转移知识库接口');
        message.success('转移知识库成功')
      }
    })
  }

  updateKnowledge = async () => {
    const { knowledge } = this.state;
    const ret = await updateKnowledgeDetail({
      ...knowledge,
      isPublic: `${Number(knowledge.isPublic)}`,
    });
    if(ret.code === 200) {
      message.success('更新知识库成功');
    }
  }

  onChangeDetail = (type, value) => {
    const { knowledge } = this.state;
    knowledge[type] = value;

    this.setState({
      knowledge,
      edited: true,
    });
  }

  renderGroupSetting = () => {
    const { knowledge, edited } = this.state;
    return (
      <div className="page-section">
        <h3>
          知识库信息
        </h3>
        <div className="main-section">
          <section className="part">
            <div className="label">名称</div>
            <Input 
              onChange={e => this.onChangeDetail('baseName', e.target.value)} 
              value={knowledge.baseName} 
              style={{width: 200, borderRadius: '4px'}} 
            />
          </section>
          <section className="part flex">
            <div className="left-section">
              <div className="label">简介</div>
              <Input.TextArea 
                onChange={e => this.onChangeDetail('baseInfo', e.target.value)} 
                value={knowledge.baseInfo} 
                style={{width: 420, borderRadius: '4px'}} 
              />
            </div>
            <Button 
              onClick={this.updateKnowledge} 
              className="updateBtn"
              disabled={!edited}
            >更新</Button>
          </section>
        </div>
      </div>
    )
  }

  renderMultiSetting = () => {
    const { options, selectedGroupId, knowledge, edited } = this.state;
    return (
      <>
        <div className="page-section">
          <h3>
            高级选项
          </h3>
          <div className="main-section flex">
            <Checkbox
              onChange={(e) => this.onChangeDetail('isPublic', e.target.checked)}
              checked={knowledge.isPublic} 
              style={{display: 'flex',}}
            >
              <h4 style={{marginTop: '-3px'}}>开启评论功能</h4>
              <p className="desc-paragraph">
                可见范围为“互联网可见”知识库默认开启，取消勾选后所有用户都无法评论。
              </p>
            </Checkbox>
            <Button 
              disabled={!edited} 
              className="updateBtn"
              onClick={this.updateKnowledge}
            >
              更新
            </Button>
          </div>
        </div>
        <div className="page-section">
          <h3>
            导出知识库
          </h3>
          <div className="main-section">
            <h4 style={{marginBottom: '21px'}}>
              可以将知识库内容批量导出，暂不支持导出表格
            </h4>
            <Button style={{background: 'none', width: '100px'}} type="default">导出</Button>
          </div>
        </div>
        <div className="page-section">
          <h3>
            转移知识库
          </h3>
          <div className="main-section">
            <h4 style={{marginBottom: '21px'}}>
              可以将知识库转移到其他团队，操作后原地址访问失效
            </h4>
            <Select 
              style={{width: '240px', marginBottom: '21px'}}
              placeholder="选择一个你有权限的团队"
              value={selectedGroupId}
              onChange={values => {
                console.log(values, 'values')
                this.setState({
                  selectedGroupId: values,
                })
              }}
            >
              {
                options.map(item => <Option key={item.value}>{item.label}</Option>)
              }
            </Select>
            <br />
            <Button 
              style={{background: 'none', color: 'red', width: '100px'}} 
              type="danger"
              onClick={() => this.onChangeGroup()}
            >
                转移
            </Button>
          </div>
        </div>
        <div className="page-section">
          <h3>
            删除知识库
          </h3>
          <div className="main-section">
            <h4 style={{marginBottom: '21px'}}>
              将这个知识库彻底删除，知识库下的所有数据将会删除
            </h4>
            <Button style={{background: 'none', color: 'red', width: '100px'}} type="danger">删除</Button>
          </div>
        </div>
      </>
    )
  }

  renderRootSetting = () => {
    return (
      <>
        <div className="page-section">
          <h3>
            成员角色权限设置
          </h3>
          <div className="main-section">
            <section className="part-paragraph">
              <h4>管理员权限</h4>
              <p>管理员拥有全部权限</p>
            </section>
            
            <h4 style={{marginBottom: '21px'}}>成员</h4>
            <section className="part-paragraph">
              <h5 className="desc-paragraph">团队权限</h5>
              <Checkbox className="checkedBox">允许成员添加、删除成员</Checkbox>
              <Checkbox className="checkedBox">允许成员新建、删除知识库</Checkbox>
            </section>
            <section className="part-paragraph">
              <h5 className="desc-paragraph">知识库权限</h5>
              <Checkbox className="checkedBox">允许成员添加、删除知识库成员</Checkbox>
              <Checkbox className="checkedBox">允许成员导出知识库</Checkbox>
              <Checkbox className="checkedBox">允许成员分享知识库</Checkbox>
              <Checkbox className="checkedBox">允许成员导出文档</Checkbox>
              <Checkbox className="checkedBox">允许成员分享文档</Checkbox>
            </section>
            <h4 style={{marginBottom: '12px'}}>只读成员</h4>
            <p>仅拥有只读权限</p>
          </div>
        </div>
      </>
    )
  }

  render() {
    const { selectedKeys } = this.state;

    return (
      <div className="group-setting">
        <div className="side-tab">
          <h3>设置</h3>
          <Menu onSelect={this.onSelect} selectedKeys={selectedKeys} className="menu" mode="inline">
            <Menu.Item key="knowledgeSetting" className="menu-item">
              知识库信息
            </Menu.Item>
            <Menu.Item key="multiSetting" className="menu-item">
              高级设置
            </Menu.Item>
            <Menu.Item key="rootSetting" className="menu-item">
              权限设置
            </Menu.Item>
          </Menu>
        </div>
        <div className="right-content">
          {
            selectedKeys[0] === 'knowledgeSetting' && this.renderGroupSetting()
          }
          {
            selectedKeys[0] === 'multiSetting' && this.renderMultiSetting()
          }
          {
            selectedKeys[0] === 'rootSetting' && this.renderRootSetting()
          }
        </div>
      </div>
    )
  }
}