import React from 'react';
import { Form, Input, Checkbox, Button, message } from 'antd';
import { LockOutlined } from '@ant-design/icons';
import { loginByUserCode } from '../../api/user';
import { actions } from 'mirrorx';
import ChangePassword from './ChangePassword/ChangePassword';
import BindPhone from './BindPhone/BindPhone';
import './index.css';

export default (props) => {
  const [userCode, password] = [localStorage.getItem('userCode'), localStorage.getItem('password')];
  const type = props.location.pathname.split('/')[1];
  const onFinish = async params => {
    const { userCode, password, remember } = params;
    const res = await loginByUserCode({ userCode, password });
    if(!res) return;
    actions.userInfo.save({
      userCode,
      password,
    });
    
    await localStorage.setItem('token', res.data);
    
    if (remember) {
      localStorage.setItem('userCode', userCode);
      localStorage.setItem('password', password);
    } else {
      localStorage.removeItem('userCode');
      localStorage.removeItem('password');
    };
    
    const { phone } = await actions.userInfo.getUserInfos();
    // if (phone) {
      message.success(res.msg);
      props.history.push("/dashboard");
    // } else {
      // message.warning('检测到您的账号还没有绑定手机号，请立即绑定');
    // }
  }

  const loginForm = () => (
    <Form
      className="login-form"
      initialValues={{
        remember: true,
        userCode,
        password,
      }}
      onFinish={(values) => onFinish(values)}
    >
      <img className="login-logo" src={require('../../assets/images/logo.png')} alt=""/>
      <h1 className="logo-title">鲸落</h1>
      <p className="logo-desc">专业的设计团队协作工具</p>
      <Form.Item
        name="userCode"
        rules={[{ required: true, message: '请输入用户名/学号' }]}
      >
        <Input 
          addonBefore="学号"
          placeholder="请输入学号"
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="password"
        rules={[{ required: true, message: '请输入密码' }]}
        style={{margin: 0}}
        >
        <Input.Password
          prefix={<LockOutlined />}
          placeholder="请输入密码"
          size="large"
        />
      </Form.Item>
      <div className="remember-box">
        <Form.Item 
          name="remember" 
          style={{margin: '12px 0 6px 0'}}
          valuePropName="checked"
        >
          <Checkbox>记住密码</Checkbox>
        </Form.Item>
        <a href="#/changePassword">修改密码</a>
      </div>
      <Button type="primary" size="large" htmlType="submit">登陆</Button>
      <a className="found-password">找回密码</a>
    </Form>
  )

  return <div className="login-page">
    <img className="login-background" src={require('../../assets/images/login_background.png')} alt=""/>
    { type === 'login' && loginForm() }
    { type === 'changePassword' && <ChangePassword history={props.history} />}
    { type === 'bindPhone' && <BindPhone /> }
  </div>
}