import React, { useState } from 'react';
import { Menu, Dropdown, Checkbox } from 'antd';
import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';

import './index.css'

export default function SelectMenu(props) {
  const { options, onChange } = props;
  const [indeterminate, setIndeterminate] = useState(false);
  const [checkedList, setCheckedList] = useState(options.map(item => item.value));
  const [isAllChecked, setIsAllChecked] = useState(true);
  const [isShowMenu, setIsShowMenu] = useState(false);

  const onCheckAllChange = e => {
    setCheckedList(e.target.checked ? options.map(item => item.value) : []);
    setIndeterminate(false);
    setIsAllChecked(e.target.checked);
    onChange(e.target.checked ? options.map(item => item.value) : []);
  }

  const menu = (checkedList) => {
    return (
      <Menu
        className="select-menu"
        onMouseLeave={() => {
          setIsShowMenu(false);
        }}
        onMouseEnter={() => {
          setIsShowMenu(true);
        }}
      >
        <Menu.Item>
          <Checkbox
            indeterminate={indeterminate}
            onChange={onCheckAllChange}
            checked={isAllChecked}
            style={{marginLeft: '4px'}}
          >
            全部
          </Checkbox>
        </Menu.Item>
        <Checkbox.Group 
          value={checkedList}
          onChange={(checkedList) => {
            setIndeterminate(!!checkedList.length && checkedList.length < options.length);
            setCheckedList(checkedList);
            onChange(checkedList);
            setIsAllChecked(checkedList.length === options.length);
          }}
          values={checkedList}
        >
          <Menu>
          {
            options.map(item => 
              <Menu.Item style={{margin: '4px 0'}} key={item.value}>
                <Checkbox value={item.value}>{item.label}</Checkbox>
              </Menu.Item>
            )
          }
          </Menu>
        </Checkbox.Group>
      </Menu>
    );
  }

  const handleVisibleChange = flag => {
    setIsShowMenu(flag);
  }

  return (
    <Dropdown 
      className="dropDown-menu" 
      visible={isShowMenu} 
      onVisibleChange={handleVisibleChange}
      overlay={menu(checkedList)}>
      <a className="ant-dropdown-link" style={{color: '#333'}} onMouseEnter={e => {
        e.preventDefault();
        setIsShowMenu(true)
      }}>
        <span style={{marginRight: '6px'}}>最近动态</span>
        {
          !isShowMenu ? 
          <CaretDownOutlined /> : 
          <CaretDownOutlined rotate={180} /> 
        }
      </a>
    </Dropdown>
  )
} 