import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import { Modal, Button } from 'antd';
import Icon from '../Icon/Icon';
import './index.css';

const DialogModal = (props) => {
  const [visible, setVisible] = useState(true)
  const { 
    title, 
    hasNoFooter, 
    content, 
    renderActionBar, 
    type = 'warning', 
    isOnlyOk, 
    onOk, 
    onCancel,
    width = 600,
  } = props;

  const renderWarning = () => {
    return (
      <div className="warn-content flex">
        <Icon className="icon" type="iconzujian63" />
        <span>{content}</span>
      </div>
    )
  }

  const renderFooter = () => {
    if ( hasNoFooter ) return null;
    return (
      <div>
        {!isOnlyOk && <Button onClick={() => setVisible(false)}>
          取消
        </Button>
        }
        <Button type="primary" onClick={() => {
          setVisible(false);
          onOk();
        }}>
          确定
        </Button>
      </div>
    )
  }

  return <Modal 
    title={title}
    visible={visible}
    okText="确定"
    cancelText="取消"
    onOk={onOk}
    onCancel={() => setVisible(false)}
    footer={renderFooter()}
    width={width}
  >
    { type === "warning" ? renderWarning() : content }
  </Modal>
}

DialogModal.show = (props) => {
  const container = document.createElement('div');
  document.body.appendChild(container);
  ReactDOM.render(<DialogModal {...props} />, container);
}

export default DialogModal;