import React, { Component } from 'react';
import { Dropdown, Menu } from 'antd';
import Icon from '../Icon/Icon';
import './index.css';
import { connect, actions } from 'mirrorx';
import { loginOut } from '../../api/user';

class Avatar extends Component {
  constructor(props) {
    super(props);
    const { detailInfos } = props;
    this.state ={
      user: {
        ...detailInfos,
        userCode: detailInfos.studentCode,
        userNick: detailInfos.nickName,
      },
      isVisible: false,
    };
  }

  async componentDidMount() {
    const detailInfos = await actions.userInfo.getUserInfos({forceUpdate: true});
    this.setState({
      user: {
        ...detailInfos,
        userCode: detailInfos.studentCode,
        userNick: detailInfos.nickName,
      },
    });
  }

  quit = async() => {
    await loginOut({userCode: this.state.user.userCode});
    localStorage.removeItem('token');
    window.location.href = '#/login';
  }

  personMenu = () => {
    const { user } = this.state;
    return (
      <Menu>
        <div className="avatar-info">
          <h2>
            {user.userNick || user.name}
            <span className="guide-text">欢迎您</span>
          </h2>
          <p className="desc-paragraph">
            {user.userCode}
          </p>
          </div>
        <Menu.Divider />
        <Menu.Item key="1">
          <Icon type="iconlujing3" />
          <a href="#/personal_center">个人中心</a>
        </Menu.Item>
        <Menu.Item key="2">
          <Icon type="iconlujing4" />
          <a href="#/personalInfo_edit">账户设置</a>
        </Menu.Item>
        <Menu.Item key="3">
          <Icon type="iconlujing9" />
          <a>帮助文档</a>
        </Menu.Item>
        <Menu.Item key="4">
          <Icon type="iconlujing7" />
          <a onClick={() => this.quit()}>退出登陆</a>
        </Menu.Item>
      </Menu>
    )
  }

  onVisibleChange = isVisible => {
    this.setState({
      isVisible,
    })
  }

  render() {
    const { isVisible } = this.state;
    return <Dropdown
      overlay={this.personMenu()}
      onVisibleChange={isVisible => this.onVisibleChange(isVisible)}
      visible={isVisible}
      placement="bottomRight"
      overlayClassName="avatar-menu"
    >
      <div>
        <img className="avatar-img" src={require('../../assets/images/default_avatar.png')} alt=""/>
        <Icon type={!isVisible ? 'iconduobianxing3' : 'iconduobianxing22'}/>
      </div>
    </Dropdown>
  }
}

export default connect(state => state.userInfo)(Avatar);