import axios from 'axios';
import { message } from 'antd';

// 后端请求接口主地址
const BASE_URL = 'http://www.jingluo.ink';

export const request = (method, api, params, needToken, needAlert) => {
  const token = localStorage.getItem('token');
  if(needToken && (!token || token === 'undefined')) {
    window.location.href = "#/login";
    return false;
  };

  let url = !needToken ? `${BASE_URL}${api}` : api.includes('?') ? `${BASE_URL}${api}&userToken=${token}` : `${BASE_URL}${api}?userToken=${token}`;
  const data = {
    ...params,
    userToken: token,
  };

  if(!needToken) {
    delete data.userToken;
  };

  return axios({
    method,
    url,
    data,
  }).then(res => {
    if (res.data.code === 400) {
      message.error(res.data.msg);
      if (res.data.msg === '登录已失效，请重新登陆') {
        window.location.href = "#/login";
      };
      return false;
    }
    return res.data;
  });
}
export const POST = (api, params, needToken = true, needAlert = false) => {
  return request('post', api, params, needToken, needAlert);
}

export const GET = (api, params, needToken, needAlert) => {
  return request('get', api, params, needToken, needAlert);
}

