import React, { Component } from 'react';
import { Chart, Util } from '@antv/g2';
import TweenOne from 'rc-tween-one';
import Children from 'rc-tween-one/lib/plugin/ChildrenPlugin';
import moment from 'moment';
import Icon from '../../../../public/Icon/Icon';
import PlaceHolder from '../../../../public/PlaceHolder';
import './GroupDataAnalysis.css';

TweenOne.plugins.push(Children);

const initl = {
  docsCount: '文档知识库',
  dashboardCount: '画板知识库',
  fileCount: '文档',
  tableCount: '表格',
  mindMapCount: '思维导图',
  extraCount: '附件',
}

export default class GroupDataAnalysis extends Component {
  constructor(props) {
    super(props);

    const date = moment().format('l');
    this.state = {
      updateTime: date,
      memberCount: 5,
      assistantCount: 20,
      knowledgeCounts: 12,
      knowledgeInfo: {

      },
      knowledgeDetail: {
        docsCount: 12,
        dashboardCount: 3,
      },
      docDetail: {
        fileCount: 900,
        tableCount: 100,
        mindMapCount: 90,
        extraCount: 21,
      },
      selectIndex: 0,
      selected: {},
      animation: {
        Children: {
          value: 691,
          floatLength: 0,
        },
        duration: 300,
      },
      writeIns: [
        { writer: '高黎明', counts: 36 },
        { writer: '焦斌', counts: 11 },
        { writer: '丁义秋', counts: 4 },
      ],
    }
  }

  componentDidMount() {
    const { docDetail, knowledgeDetail, writeIns } = this.state;
    this.renderChart(document.getElementsByClassName('chart_one')[0], knowledgeDetail);
    this.renderChart(document.getElementsByClassName('chart_one')[1], docDetail);
    this.renderHeightChart(document.getElementById('chart_t'), writeIns);
  }
  
  sum = (keysArr, obj) => {
    let v = 0;
    keysArr.map(key => {
      v += obj[key]
    });
    return v;
  }

  renderChart(DOMElement, data) {
    const keysArr = Object.keys(data);
    const sum = this.sum(keysArr, data);

    const fits = keysArr.map(key => {
      return {
        type: initl[key],
        value: Number((data[key] / sum).toFixed(2)),
        count: data[key],
      }
    });
    const chart = new Chart({
      container: DOMElement,
      autoFit: true,
      height: 160,
      width: 400,
    });
    chart.data(fits);
    chart.coordinate('theta', {
      radius: 0.75,
      innerRadius: 0.6,
    });
    chart.scale('value', {
      formatter: (val) => {
        val = parseInt(val * 100) + '%';
        return parseInt(val) + '%';
      },
    });
    chart.tooltip({
      showTitle: false,
      showMarkers: false,
    });
    chart.axis(false);
    chart.legend('item', {
      position: 'right',                                  // 配置图例显示位置
      custom: true,                                       // 关键字段，告诉 G2，要使用自定义的图例
      items: fits.map((obj, index) => {
        return {
          name: obj.type,                                 // 对应 itemName
          value: obj.value,                             // 对应 itemValue
          marker: {
            symbol: 'square',                             // marker 的形状
            style: {
              r: 5,                                       // marker 图形半径
              fill: chart.getTheme().colors10[index],     // marker 颜色，使用默认颜色，同图形对应
            },
          },                                              // marker 配置
        };
      }),
      itemValue: {
        style: {
          fill: '#999',
        },                                               // 配置 itemValue 样式
        formatter: val => `${val * 100}%`                // 格式化 itemValue 内容
      },
    });  
    
    const interval = chart
    .interval()
    .adjust('stack')
    .position('value')
    .color('type')
    .style({
      fillOpacity: 1,
    })
    .state({
      active: {
        style: element => {
          const shape = element.shape;
          return {
            lineWidth: 10,
            stroke: shape.attr('fill'),
            strokeOpacity: shape.attr('fillOpacity'),
          };
        },
      },
    });  
    // chart.interaction('element-single-selected');

    // 移除图例点击过滤交互
    // chart.removeInteraction('legend-filter');
    chart.interaction('element-active');
    chart.render();


    let lastItem;
    function updateAnnotation(data) {
      if (data.item !== lastItem) {
        chart.annotation().clear(true);
        chart
          .annotation()
          .text({
            position: ['50%', '50%'],
            content: data.type,
            style: {
              fontSize: 12,
              fill: '#8c8c8c',
              textAlign: 'center',
            },
            offsetY: -10,
          })
          .text({
            position: ['50%', '50%'],
            content: data.count,
            style: {
              fontSize: 16,
              fill: '#333',
              textAlign: 'center',
            },
            offsetX: -10,
            offsetY: 20,
          })
          .text({
            position: ['48%', '50%'],
            content: '个',
            style: {
              fontSize: 16,
              fill: '#8c8c8c',
              textAlign: 'center',
            },
            offsetY: 20,
            offsetX: 20,
          });
        chart.render(true);
        lastItem = data.item;
      }
    }

    // 清空 annotation
    function clearAnnotation() {
      chart.annotation().clear(true);
      chart.render(true);
      lastItem = null;
    };

    chart.on('element:statechange', (ev) => {
      const { state, stateStatus, element } = ev.gEvent.originalEvent;
    
      // 本示例只需要监听 active 的状态变化
      if (state === 'active') {
        const data = element.getData();
        if (stateStatus) {
          // 更新 Annotation
          updateAnnotation(data);
        } else {
          // 隐藏 Annotation
          clearAnnotation();
        }
      }
    });
  }

  renderHeightChart(DOMElement, data) {
    const chart = new Chart({
      container: DOMElement,
      width: 400,
      height: 180,
    });

    chart.data(data);
    chart.interval().position('writer*counts');
    chart.render();
  }

  render() {
    const { updateTime, animation } = this.state;
    console.log(animation)
    return (
      <div className="analysis">
        <h3 className="topTitle">统计 &nbsp;
          <span className="desc-paragraph">|&nbsp;&nbsp;{updateTime}（每日上午10：00更新昨日数据）</span>
        </h3>
        <section className="firstData flex">
          <div className="card">
            <h2 className="title">成员总数</h2>
            <h2 className="numberCount">6</h2>
            <p className="desc-paragraph">协作人数: 0</p>
          </div>
          <div className="card flex">
            <div className="box">
              <h2 className="title">知识库总数<Icon type="iconzu416" /></h2>
              <h2 className="numberCount">
                15
              </h2>
              <p className="desc-paragraph">
                <span>文档知识库 12</span>
                <span>画板知识库 3</span>
              </p>
            </div>
            <div className="chart_one"></div>
          </div>
          <div className="card">
            <div className="box">
              <h2 className="title">知识沉淀总数</h2>
              <h2 className="numberCount">
                1121
              </h2>
              <div>
                <br/>
              </div>
            </div>
            <div className="chart_one"></div>
          </div>
        </section>
        <div className="analysis-content flex">
          <div className="mainWrap-content">
            <div className="title">
              <dl>
                <TweenOne
                  animation={{
                    Children: {
                      floatLength: 0,
                      value: 791,
                    },
                    duration: 1500,
                  }}
                  style={{fontSize: 36,}}
                >
                  0
                </TweenOne>
                <dd>次协同</dd>
              </dl>
              <span>历史累计</span>
            </div>
            <div className="list-content">
              <dl>
                <TweenOne
                  animation={this.state.animation}
                  style={{fontSize: 28,}}
                >
                  0
                </TweenOne>
                <dd>阅读</dd>
              </dl>
              <dl>
                <TweenOne
                  animation={{
                    Children: {
                      floatLength: 0,
                      value: 171,
                    },
                    duration: 300,
                  }}
                  style={{fontSize: 28,}}
                >
                  0
                </TweenOne>
                <dd>编辑</dd>
              </dl>
              <dl>
                <TweenOne
                  animation={{
                    Children: {
                      floatLength: 0,
                      value: 128,
                    },
                    duration: 300,
                  }}
                  style={{fontSize: 28,}}
                >
                  0
                </TweenOne>
                <dd>评论</dd>
              </dl>
            </div>
          </div>
          <div className="heightChart" id="chart_t">
            <h3 className="flex">
              <span>成员贡献排行榜</span>
              <span>最近30日</span>
            </h3>
          </div>
        </div>
        
        <div className="bottomWrap">
          <PlaceHolder className="placeHolder" title="数据统计详情" />  
        </div>
      </div>
    )
  }
}